import './CastingItem.css';

function CastingItem({ casting, onCastingClick }) {
  function handleCastingClick() {
    onCastingClick(casting)
  }

  return (
    <div className='casting-info' onClick={handleCastingClick}>
      <p className='casting-info__date'>{casting.date}</p>
      <p className='casting-info__city'>{casting.city}</p>
      <p className='casting-info__price'><span className='casting-info__price-icon'/>{casting.price} каст</p>
      <h2 className='casting-info__title'>{casting.title}</h2>
      <ul className='casting-info__tags'>
        {casting.tags.slice(0, casting.tags.length).map((tag) =>
          (<li className='casting-info__tag' key={tag}>{tag}</li>)
        )}
      </ul>
    </div>
  )
}

export default CastingItem;