import './Tabs.css';

function Tabs({ handleButtonClick, activeTab, tabs }) {

  return (
    <div className="tabs__buttons">
      {tabs.slice(0, tabs.length).map((tab, index) =>
        (<button data-name={tab.value} key={index} onClick={handleButtonClick} className={`tabs__button ${activeTab === tab.value ? 'tabs__button_active' : ''}`}>{tab.text}</button>)
      )}
    </div>
  )
}

export default Tabs;