import './CheckboxFilter.css';
import { Link } from 'react-router-dom';

function CheckboxFilter({register, title, checkBoxClassName, link, name}) {
  
  return (
    <label className={checkBoxClassName ? checkBoxClassName : 'filter-checkbox'}>
      <input
        {...register(name)}
        type='checkbox'
        className='filter-checkbox_invisible'
      />
      <span className='filter-checkbox_visible'/>
      <p className='filter-checkbox__title'>{title}</p>
      {link && <Link to='/question-list' className='filter-checkbox__link'>Подробнее о гаранте</Link>}
    </label>
  )
}

export default CheckboxFilter;