import './NewCasting.css';
import { useForm } from 'react-hook-form';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer'
import GoBackButton from '../../components/GoBackButton/GoBackButton';
import NewCastingInputs from '../../components/NewCastingInputs/NewCastingInputs';
import BalanceInfo from '../../components/BalanceInfo/BalanceInfo';
import MultiSelectFilter from './MultiSelectFilter/MultiSelectFilter';
import { ACTORS_OPTIONS } from '../../constants/filterOptions';

function NewCasting({loggedIn}) {
  const { 
    register, handleSubmit, formState: { errors }, control, watch
  } = useForm({
    mode: "onChange",
  });

  const watchUseGrant = watch('useGrant')
  const watchPrice = watch('price')

  const onSubmit = (data) => {
    console.log(data)
  }

  return (
    <>
      <Header loggedIn={loggedIn}/>
      <main className='content content_new-casting'>
        <GoBackButton />
        <h1 className='new-casting__title'>Новый кастинг</h1>
        <form className='new-casting__form' onSubmit={handleSubmit(onSubmit)}>
          <div className='new-casting__form-container'>
            <NewCastingInputs register={register} control={control} errors={errors} watch={watch} />
            <MultiSelectFilter
              title={'Пригласить актеров на кастинг'}
              options={ACTORS_OPTIONS}
              control={control}
              nameInput={'actors'}
              placeholder={'Введите значение'}
            />
            <p className='new-casting__choose-actor-text'>Введите имена авторизованных актеров</p>
          </div>
          <div className='new-casting__container'>
            {watchUseGrant && <BalanceInfo toPay={watchPrice}/>}
            <button className='new-casting__button' type='submit'>Разместить заказ</button>
          </div>
        </form>
      </main>
      <Footer />
    </>
  )
}

export default NewCasting;