import './BodyInfoInputs.css';
import TextInput from '../TextInput/TextInput';
import SelectFilter from '../SelectFilter/SelectFilter';
import { REQUIRED_ERROR, LENGTH_ERROR } from '../../constants/errors';
import {
  BODY_TIPE_OPTIONS,
  SKIN_TIPE_OPTIONS,
  BODY_HAIR_OPTIONS,
  LOOKS_TYPE_OPTIONS,
  HAIR_LENGTH_OPTIONS,
  HAIR_FORM_OPTIONS,
  GENDER_OPTIONS
} from '../../constants/filterOptions'

function BodyInfoInputs ({ bodyFilterClassName, register, control, errors }) {
  
  const questionsClassName = (
    `${bodyFilterClassName ? bodyFilterClassName : ''}`
  )

  return (
    <div className={questionsClassName}>
      <div className='filter-radio'>
        <h3 className='filter-radio__title'>Пол</h3>
          <div className='filter-radio__container'>
            {GENDER_OPTIONS.slice(1, GENDER_OPTIONS.length).map((option, index) => (
            <label className='filter-radio__label' key={option.id}>
              <input
                {...register('gender', {
                  required: REQUIRED_ERROR,
                })}
                type='radio'
                value={option.value}
                className='filter-radio__input filter-radio__input_invisible'
              />
              <span className='filter-radio__input_visible'/>
              <p className='filter-radio__label-subtitle'>{option.label}</p>
            </label>
            ))}
            {errors.gender && (
            <div className='input__error'>
              {errors.gender.message}
            </div>
            )}
          </div>
        </div>
        <div className='questionary__questions questionary__questions_step_second'>
          <TextInput
            title={'Объем талии, см'}
            placeholder={'Введите значение'}
            nameInput={'waist'}
            errorValue={REQUIRED_ERROR}
            type='number'
            register={register}
            errors={errors}
            minLength ={'2'}
            minLengthError={LENGTH_ERROR}
          />
          <TextInput
            title={'Объем бедер, см'}
            placeholder={'Введите значение'}
            nameInput={'hips'}
            errorValue={REQUIRED_ERROR}
            type='number'
            register={register}
            errors={errors}
            minLength ={'2'}
            minLengthError={LENGTH_ERROR}
          />
          <SelectFilter options={BODY_TIPE_OPTIONS} title={'Телосложение'} control={control} nameInput={'body'} rules={{required: REQUIRED_ERROR}} />
          <SelectFilter options={SKIN_TIPE_OPTIONS} title={'Цвет кожи'} control={control} nameInput={'skin'} rules={{required: REQUIRED_ERROR}} />
          <SelectFilter options={BODY_HAIR_OPTIONS} title={'Наличие волос на теле'} control={control} nameInput={'bodyHair'} rules={{required: REQUIRED_ERROR}} />
          <SelectFilter options={LOOKS_TYPE_OPTIONS} title={'Внешность'} control={control} nameInput={'looks'} rules={{required: REQUIRED_ERROR}} />
          <SelectFilter options={HAIR_LENGTH_OPTIONS} title={'Длина волос'} control={control} nameInput={'hairLength'} rules={{required: REQUIRED_ERROR}} />
          <SelectFilter options={HAIR_FORM_OPTIONS} title={'Форма волос'} control={control} nameInput={'hairForm'} rules={{required: REQUIRED_ERROR}} />
        </div>
    </div>
  )
}

export default BodyInfoInputs;