import './Popup.css';

function Popup( {isOpen, onClose }) {
  return (
    <div
      onClick={e => (e.currentTarget === e.target) && onClose()}
      className={`popup-payment ${isOpen && "popup-payment_opened"}`}
    >
      <div className="popup-payment__container">
        <button className='popup-payment__button_close' onClick={onClose}/>
        <span className='popup-payment__icon'/>
        <h2 className='popup-payment__title'>Платеж успешно отправлен</h2>
        <p className='popup-payment__text'>
          Срок обработки платежа зависит от вашего банка, но не более 3 суток.
        </p>
        <button className='popup-payment__button' onClick={onClose}>Отлично!</button>
      </div>
    </div>
  )
}

export default Popup;