import { useState, useEffect } from 'react';
import './RangeSlider.css';
import Slider from '@mui/material/Slider';

function RangeSlider({register, errors}) {
  const [minAge, setMinAge] = useState(18);
  const [maxAge, setMaxAge] = useState(55);

  const ageMin = register('ageMin');
  const ageMax = register('ageMax');

  const handleSliderChange = (event, newValue) => {
    setMinAge(newValue[0]);
    setMaxAge(newValue[1]);
  };

  const handleInputMinAge = (event) => {
    setMinAge(event.target.value === '' ? '' : Number(event.target.value));
  };

  const handleInputMaxAge = (event) => {
    setMaxAge(event.target.value === '' ? '' : Number(event.target.value));
  };

  const handleBlurMinAge = () => {
    if (minAge <= 0 || minAge > maxAge) {
      setMinAge(0);
    } else if (minAge > 100) {
      setMinAge(0);
    }
  };

  const handleBlurMaxAge = () => {
    if (maxAge <= 0 || maxAge < minAge) {
      setMaxAge(100);
    } else if (maxAge > 100) {
      setMaxAge(100);
    }
  };

  useEffect(() => {
  if (minAge < 0 ) {
      setMinAge(0);
  } else if (minAge > 100) {
      setMinAge(0);
  }}, [minAge])
  
  useEffect(() => {
  if (maxAge < 0) {
    setMaxAge(100);
  } else if (maxAge > 100) {
    setMaxAge(100);
  }}, [maxAge])

  return (
    <div className='range-slider'>
      <h3 className='range-slider__title'>Возраст</h3>
      <div className='range-slider__container'>
        <input
          name={ageMin.name}
          className='range-slider__input'
          value={minAge}
          onChange={(e) => {
            ageMin.onChange(e)
            handleInputMinAge(e);
          }}
          onBlur={(e) => {
            handleBlurMinAge(e);
          }}
          type='number'
        />
        <Slider
          value={[minAge, maxAge]}
          onChange={handleSliderChange}
        />
        <input
          name={ageMax.name}
          className='range-slider__input'
          onChange={(e) => {
            ageMax.onChange(e)
            handleInputMaxAge(e);
          }}
          onBlur={(e) => {
            handleBlurMaxAge(e);
          }}
          value={maxAge}
          type='number'
        />
      </div>
    </div>
  );
}

export default RangeSlider;