import './PromoCasting.css';

function PromoCasting({ city, price, title, tags }) {
  return (
    <div className='promo-casting'>
      <p className='promo-casting__city'>{city}</p>
      <p className='promo-casting__price'><span className='promo-casting__icon'/>{price} ₽</p>
      <h3 className='promo-casting__title'>{title}</h3>
      <ul className='promo-casting__tags'>
        {tags.slice(0, tags.length).map((tag, index) =>
          (<li className='promo-casting__tag' key={index}>{tag}</li>)
        )}
      </ul>
    </div>
  )
}

export default PromoCasting;