import './InterestsInputs.css';
import TextareaInput from '../TextareaInput/TextareaInput';
import SelectFilter from '../SelectFilter/SelectFilter';
import { REQUIRED_ERROR } from '../../constants/errors';

import {
  SMOKING_ATTITUDE_OPTIONS,
  ALCOHOL_ATTITUDE_OPTIONS,
  PSYCHOTROPIC_SUBSTANCES_ATTITUDE_OPTIONS,
  ORIENTATION_TYPE_OPTIONS,
  MARITAL_STATUS_OPTIONS,
  CHILDREN_STATUS_OPTIONS
} from '../../constants/filterOptions'

function InterestsInputs({ interestsFilterClassName, control, errors, register }) {
  const questionsClassName = (
    `${interestsFilterClassName ? interestsFilterClassName : ''}`
  )
  
  return (
    <div className={questionsClassName}>
      <div className='questionary__questions questionary__questions_step_third'>
        <div className='questionary__container questionary__container_attitude'>
        <SelectFilter options={SMOKING_ATTITUDE_OPTIONS} title={'Отношение к курению'} control={control} nameInput={'smokingAttitude'} rules={{required: REQUIRED_ERROR}} />
        <SelectFilter options={ALCOHOL_ATTITUDE_OPTIONS} title={'Отношение к алкоголю'} control={control} nameInput={'alcoholAttitude'} rules={{required: REQUIRED_ERROR}} />
        <SelectFilter options={PSYCHOTROPIC_SUBSTANCES_ATTITUDE_OPTIONS} title={'Отношение к психотропным веществам'} control={control} nameInput={'psychotropicSubstancesAttitude'} rules={{required: REQUIRED_ERROR}} />
        </div>
        <div className='questionary__container questionary__container_interests'>
          <SelectFilter options={ORIENTATION_TYPE_OPTIONS} title={'Ориентация'} control={control} nameInput={'orientation'} rules={{required: REQUIRED_ERROR}} />
          <SelectFilter options={MARITAL_STATUS_OPTIONS} title={'Семейное положение'} control={control} nameInput={'maritalStatus'} rules={{required: REQUIRED_ERROR}} />
          <SelectFilter options={CHILDREN_STATUS_OPTIONS} title={'Наличие детей'} control={control} nameInput={'childrens'} rules={{required: REQUIRED_ERROR}} />
        </div>
      </div>
      <div className='questionary__text-areas'>
        <TextareaInput
          title={'Увлечения'}
          placeholder={'Чем вы любите заниматься'}
          nameInput={'hobbies'}
          errorValue={REQUIRED_ERROR}
          register={register}
          errors={errors}
        />
        <TextareaInput
          title={'О себе'}
          placeholder={'Напишите о себе'}
          nameInput={'about'}
          errorValue={REQUIRED_ERROR}
          register={register}
          errors={errors}
        />
      </div>
    </div>
  )
}

export default InterestsInputs;