import './CastingProfile.css';
import { useState } from 'react';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import GoBackButton from '../../components/GoBackButton/GoBackButton';
import CastingDetails from './CastingDetails/CastingDetails';
import CastingMainInfo from './CastingMainInfo/CastingMainInfo';
import Popup from './Popup/Popup';
import CastingReviews from './CastingReviews/CastingReviews';
import { Link } from 'react-router-dom';

import {
  STATUS_ACTIVE,
  STATUS_IN_PROCESS,
  STATUS_INVITED,
  STATUS_ACTOR_CHOISE,
  STATUS_PASSED,
} from '../../constants/const';

function CastingProfile({ loggedIn, casting }) {
  const [isPopupConfirmOpen, setPopupConfirmOpen] = useState(false);

  function handleButtonConfirmClick () {
    setPopupConfirmOpen(true)
  }

  function closeConfirmPopup () {
    setPopupConfirmOpen(false)
  }

  return (
    <>
      <Header loggedIn={loggedIn}/>
      <main className='content content_casting-profile casting-profile'>
        <GoBackButton />
        <div className='casting-profile__buttons'>
          {casting.status === STATUS_ACTIVE ?
          <>
            <p className='casting-profile__cast-success'>
              <span className='casting-profile__cast-success-icon'/>
              Вас выбрали
            </p>
            <button className='casting-profile__button casting-profile__button_chat'>
              <span className='casting-profile__button-icon_chat'/>
              Чат с заказчиком
              <span className='casting-profile__number-notice'>5</span>
            </button>
          </>
            : casting.status === STATUS_IN_PROCESS ? 
          <>
            <button className='casting-profile__button casting-profile__button_cansel'>
              <span className='casting-profile__button-icon_cansel'/>
              Отменить заяку
            </button>
            <button className='casting-profile__button casting-profile__button_go-room'>Перейти в комнату кастинга</button>
          </> 
          : casting.status === STATUS_INVITED ? 
          <>
            <p className='casting-profile__cast-price'>
              <span className='casting-profile__cast-price-icon'/>
              Стоимость отклика 900 кастов
            </p>
            <button className='casting-profile__button casting-profile__button_confirm'>Принять приглашение</button>
          </> 
          : casting.status === STATUS_ACTOR_CHOISE ? 
          <>
            <p className='casting-profile__cast-price'>
              <span className='casting-profile__cast-price-icon'/>
              Стоимость отклика 900 кастов
            </p>
            <button className='casting-profile__button casting-profile__button_confirm' onClick={handleButtonConfirmClick}>Откликнуться</button>
          </>
          : casting.status === STATUS_PASSED && !casting.arbitration ?
          <>
            <p className='casting-profile__cast-passed'>Пройден</p>
          </>
          : casting.status === STATUS_PASSED && casting.arbitration ?
          <>
            <p className='casting-profile__cast-arbitration'>Арбитраж</p>
            <button className='casting-profile__button casting-profile__button_go-room'>Перейти в комнату кастинга</button>
          </> : ''
          }
        </div>
        <CastingMainInfo casting={casting}/>
        {
          casting.status !== STATUS_PASSED ?
          <CastingDetails casting={casting}/> 
          :
          <Link to='reviews' className='casting-profile__link'>
            <CastingReviews />
          </Link>
        }
      </main>
      <Footer />
      <Popup onClose={closeConfirmPopup} isOpen={isPopupConfirmOpen}/>
    </>
  )
}

export default CastingProfile;