import { useState } from 'react'
import MainInfo from './MainInfo/MainInfo';
import FirstCastingInfo from './FirstCastingInfo/FirstCastingInfo'

function Form() {
  const [page, setPage] = useState(1);

  function handleButtonNextClick() {
    setPage(page+1)
  };

  function handleButtonBackClick() {
    setPage(page-1)
  };

  let step = null;

  if (page === 1) {
    step = <MainInfo handleButtonNextClick={handleButtonNextClick}/>
  } else if (page === 2) {
    step = <FirstCastingInfo handleButtonBackClick={handleButtonBackClick}/>
  }

  const titleText = (
    page === 1 ? 'Заполнение анкеты' : 'Первый кастинг'
  )

  return (
    <>
      <h1 className='question-list__title question-list__title_customer'>{titleText}</h1>
      <div className='question-list__form'>
        {step}
      </div>
    </>
  )
}

export default Form;