import './SliderSection.css';
import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import SliderItem from './SliderItem/SliderItem';

import { photos } from '../../../../constants/const'

function SampleNextArrow(props) {
  const { style, onClick } = props;
  return (
    <div
      className='slider-section__arrow slider-section__arrow_next'
      style={{ ...style}}
      onClick={onClick}
    />
  );
}

function SamplePrevArrow(props) {
  const { style, onClick } = props;
  return (
    <div
      className='slider-section__arrow slider-section__arrow_prev'
      style={{ ...style }}
      onClick={onClick}
    />
  );
}

export default class SliderSection extends Component {
  render() {
    this.myRef = React.createRef();
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 1,
      nextArrow: <SampleNextArrow />,
      prevArrow: <SamplePrevArrow />
    };
    
    return (
      <div>
        <Slider {...settings} >
          {photos.map((photo, index) =>
            <SliderItem photo={photo} key={photo.id} openPopup={this.props.openPopup} setInitialPhoto={this.props.setInitialPhoto}/>
          )}
        </Slider>
      </div>
    );
  }
}