import './MyCasting.css';
import { Link } from 'react-router-dom';
import CastingItem from '../../../components/CastingItem/CastingItem';
import {
  STATUS_ACTIVE,
  STATUS_IN_PROCESS,
  STATUS_INVITED,
  STATUS_ACTOR_CHOISE,
  STATUS_PASSED,
  STATUS_ACTIVE_TEXT,
  STATUS_IN_PROCESS_TEXT,
  STATUS_INVITED_TEXT,
  STATUS_ACTOR_CHOISE_TEXT,
  STATUS_PASSED_TEXT,
  CONFIRM_CAST,
  CAST_NOTICE
} from '../../../constants/const';
import { ROUTES } from '../../../constants/routes';

function MyCasting({ casting, onCastingClick }) {

  const buttonChatClassName = (
    `my-castings__item-notice ${casting.newNotice ? 'my-castings__item-notice_new' : casting.chat ? 'my-castings__item-notice_chat' : ''}`
  )

  let statusClassName 

    if (casting.status === STATUS_ACTIVE) {
      statusClassName = 'my-castings__item-status_active'
    } else if (casting.status === STATUS_IN_PROCESS) {
      statusClassName = 'my-castings__item-status_in-process'
    } else if (casting.status === STATUS_INVITED) {
      statusClassName = 'my-castings__item-status_invited'
    } else if (casting.status === STATUS_ACTOR_CHOISE) {
      statusClassName = 'my-castings__item-status_actor-choice'
    } else if (casting.status === STATUS_PASSED) {
      statusClassName = 'my-castings__item-status_passed'
    }

  let statusText

    if (casting.status === STATUS_ACTIVE) {
      statusText = STATUS_ACTIVE_TEXT
    } else if (casting.status === STATUS_IN_PROCESS) {
      statusText = `${STATUS_IN_PROCESS_TEXT} ${casting.castTime}`
    } else if (casting.status === STATUS_INVITED) {
      statusText = STATUS_INVITED_TEXT
    } else if (casting.status === STATUS_ACTOR_CHOISE) {
      statusText = STATUS_ACTOR_CHOISE_TEXT
    } else if (casting.status === STATUS_PASSED) {
      statusText = STATUS_PASSED_TEXT
    }

  const spanClassName = (
    `my-castings__item-span ${casting.status === STATUS_IN_PROCESS || casting.status === STATUS_INVITED ? 'my-castings__item-span_visible' : ''}`
  )
  
  let spanText
  if (casting.status === STATUS_IN_PROCESS) {
    spanText = CAST_NOTICE
  } else if (casting.status === STATUS_INVITED) {
    spanText = CONFIRM_CAST
  }


  return (
    <Link to={ROUTES.CASTING_PROFILE} className='my-castings__item'>
      <CastingItem casting={casting} onCastingClick={onCastingClick}/>
      <div className='my-castings__item-container'>
        <p className={`my-castings__item-status ${statusClassName}`}>{statusText}</p>
        <span className={spanClassName}>{spanText}</span>
        <button className={buttonChatClassName}/>
      </div>
    </Link>
  )
}

export default MyCasting;