import './WithdrawFunds.css';
import { useState, useEffect } from 'react';
import { useForm } from "react-hook-form";
import {  WITHDRAWFUNDS_OPTIONS } from '../../../constants/filterOptions'
import { REQUIRED_ERROR } from '../../../constants/errors';
import TextInput from '../../../components/TextInput/TextInput';
import RadioFilter from '../../../components/RadioFilter/RadioFilter';

function WithdrawFunds() {
  const [sum, setSum] = useState();
  const [focus, setFocus] = useState(false);
  const [toggleValue, setToggleValue] = useState('card');

  const { 
    register, handleSubmit, formState: { errors }, watch,
  } = useForm({
    mode: "onChange"
  });
  
  const onSubmit = (data) => {
    console.log(data)
  }

  const commissionClassName = (
    `${focus ? 'withdraw__commission' : 'withdraw__commission_invisible'}`
  );

  const watchSumValue = watch('sumValue')

  useEffect(() => {
    setSum(watchSumValue)
  }, [watchSumValue])

  const handleChangeTypeAccount = (e) => {
    setToggleValue(e.target.value)
  }

  const handleSumFocus = (e) => {
    setFocus(true)
  };

  const handleSumBlur = (e) => {
    setFocus(false)
  };

  const commissionValue = () => {
    if(typeof(sum) === 'undefined') {
      return 0
    } else {
      return `${sum * 5 /100}`
    }
  }

  return (
    <form className='withdraw tabs__form' onSubmit={handleSubmit(onSubmit)}>
      <div className='withdraw__info-container'>
        <h2 className='withdraw__title'>Вывод средств</h2>
        <p className='withdraw__subtitle'>Минимальная сумма вывода составляет 1000 кастов + 5% комиссия сервиса. Сам банк также может назначать комиссию за перевод.</p>
        <p className='withdraw__ratio'><span className='withdraw__ratio-icon'/>Курс: 1 каст = 1 рубль</p>
      </div>
      <div className='withdraw__sum-container'>
        <label className='input__title'>
          Сумма вывода
          <input
            {...register('sumValue', {
              required: 'Поле обязательно к заполнению',
              min: {
                value: 1,
                message: "Введите положительное значение",
              },
            })}
            className='input'
            placeholder='Введите сумму'
            onFocus={handleSumFocus}
            onBlur={handleSumBlur}
            type='number'
          />
          {errors?.sumValue && (
            <div className='input__error'>
              {errors.sumValue.message}
            </div>
          )}
        {errors.sumValue ?
          ''
          :
          <p className={commissionClassName}>Комиссия составит: {commissionValue()} рублей</p>
        }
        </label>
        <RadioFilter
          options={WITHDRAWFUNDS_OPTIONS}
          title={'Куда вывести'}
          register={register} 
          nameInput={'typeAccount'}
          errors={errors}
          errorValue={REQUIRED_ERROR}
          handleInputChange={handleChangeTypeAccount}
        />
      </div>
        {
          toggleValue === 'card' ?
          <div className='withdraw__data-container withdraw__data-container_type_card'>
            <TextInput
              title={'Номер карты'}
              placeholder={'Введите номер'}
              nameInput={'cardNumber'}
              errorValue={REQUIRED_ERROR}
              type='number'
              register={register}
              errors={errors}
              minValue='1'
              message='Введите положительное значение'
            />
            <TextInput
              title={'Владелец каты'}
              placeholder={'Как на карте'}
              nameInput={'cardOwner'}
              errorValue={REQUIRED_ERROR}
              type='text'
              register={register}
              errors={errors}
              minValue='1'
              message='Введите положительное значение'
            />
          </div>
          :
          <div className='withdraw__data-container withdraw__data-container_type_account'>
            <TextInput
              title={'Номер счета'}
              placeholder={'Номер вашего счета'}
              nameInput={'account'}
              errorValue={REQUIRED_ERROR}
              type='number'
              register={register}
              errors={errors}
              minValue='1'
              message='Введите положительное значение'
            />
            <TextInput
              title={'Название банка'}
              placeholder={'Банк'}
              nameInput={'bank'}
              errorValue={REQUIRED_ERROR}
              type='text'
              register={register}
              errors={errors}
              minValue='1'
              message='Введите положительное значение'
            />
            <TextInput
              title={'БИК'}
              placeholder={'БИК банка'}
              nameInput={'bik'}
              errorValue={REQUIRED_ERROR}
              type='number'
              register={register}
              errors={errors}
              minValue='1'
              message='Введите положительное значение'
            />
          </div>
        }
      <button className='withdraw__button' type='submit'>Вывести</button>
    </form>
  )
}

export default WithdrawFunds;