import './SearchForm.css';
import useScreenWidth from "../../utils/useScreenWidth";

function SearchForm({ onButtonClick, isOpenFilter, placeholder, isDirty }) {
  const size =  useScreenWidth();
  
  const inputClassName = (`${isOpenFilter ? 'searh-form__input_display_none' : 'searh-form__input'}`)

  return (
    <div className='searh-form'>
      <form className='searh-form__container'>
        <input className={inputClassName} placeholder={placeholder}/>
        <div className='searh-form__buttons'>
          {size.width > 768 ? ''
            : <button className='searh-form__button_submit' type='submit'/>
          }
          <button className='searh-form__button_open-filter' onClick={onButtonClick} type='button'>
            <span className={`${isDirty && 'searh-form__filter-set'}`}/>
          </button>
        </div>
      </form>
    </div>
  );
}

export default SearchForm;