import './ProfilePromotion.css';
import { useState } from 'react';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import GoBackButton from '../../components/GoBackButton/GoBackButton';
import schemaBase from '../../images/scheme-profile-base.png';
import schemaPromotion from '../../images/scheme-profile-promotion.png';
import BalanceInfo from '../../components/BalanceInfo/BalanceInfo';
import { PROMOTION_ACCAUNT_IN_GENERAL_LIST, PROMOTION_ACCAUNT_IN_SIDE_BANNERS } from '../../constants/const';

function ProfilePromotion({ loggedIn }) {
  const [promotionInGeneralList, setPromotionInGeneralList] = useState(PROMOTION_ACCAUNT_IN_GENERAL_LIST[1].value);
  const [promotionInSideBanners, setPromotionInSideBanners] = useState(PROMOTION_ACCAUNT_IN_SIDE_BANNERS[0].value);

  const [pricePromotionInGeneralList, setPricePromotionInGeneralList] = useState(PROMOTION_ACCAUNT_IN_GENERAL_LIST[1].price);
  const [pricePromotionInSideBanners, setPricePromotionInSideBanners] = useState(PROMOTION_ACCAUNT_IN_SIDE_BANNERS[0].price);

  let sumToPay = pricePromotionInGeneralList + pricePromotionInSideBanners

  return (
    <>
      <Header loggedIn={loggedIn}/>
        <main className='content content_profile-promotion profile-promotion'>
          <GoBackButton />
          <h1 className='profile-promotion__title'>Продвижение профиля</h1>
          <form className='profile-promotion__form'>
            <div className='profile-promotion__container profile-promotion__container_base'>
              <img className='profile-promotion__schema profile-promotion__schema_base' src={schemaBase} alt='Схема профайл в общей ленте'/>
              <h2 className='profile-promotion__form-title profile-promotion__form-title_base'>В общей ленте</h2>
              <p className='profile-promotion__form-subtitle profile-promotion__form-subtitle_base'>Поднимает анкету на первое место</p>
              <div className='profile-promotion__inputs profile-promotion__inputs_base'>
                {PROMOTION_ACCAUNT_IN_GENERAL_LIST.slice(0, PROMOTION_ACCAUNT_IN_GENERAL_LIST.length).map((option) =>
                  <label className='profile-promotion__label' key={option.id}>
                    {option.popular && <span className='profile-promotion__span profile-promotion__span_popular'>Популярно</span>}
                    <input
                      name ='base'
                      type='radio'
                      value={option.value}
                      className='profile-promotion__input profile-promotion__input_invisible'
                      onClick={(e) =>{
                        if (e.target.value === promotionInGeneralList) {
                        setPromotionInGeneralList(null)
                        setPricePromotionInGeneralList(null)
                        } else {
                        setPromotionInGeneralList(e.target.value)
                        setPricePromotionInGeneralList(option.price)
                      }}}
                    />
                    <div className={`profile-promotion__input_visible ${promotionInGeneralList === option.value ? 'profile-promotion__input_checked' : 'profile-promotion__input_not-checked'}`}>
                      <h3 className='profile-promotion__input-title'>{option.label}</h3>
                      <p className='profile-promotion__input-subtitle'>{`${option.price} каст`}</p>
                    </div>
                  </label>
                )}
              </div>
            </div>
            <div className='profile-promotion__container profile-promotion__container_promotion'>
              <img className='profile-promotion__schema profile-promotion__schema_promotion' src={schemaPromotion} alt='Схема профайл в боковых банерах'/>
              <h2 className='profile-promotion__form-title profile-promotion__form-title_promotion'>Боковые баннеры</h2>
              <p className='profile-promotion__form-subtitle profile-promotion__form-subtitle_promotion'>Выводим вашу анкету в справа или слева от ленты</p>
              <div className='profile-promotion__inputs profile-promotion__inputs_promotion'>
                {PROMOTION_ACCAUNT_IN_SIDE_BANNERS.slice(0, PROMOTION_ACCAUNT_IN_SIDE_BANNERS.length).map((option) =>
                  <label className='profile-promotion__label' key={option.id}>
                    {option.discount && <span className='profile-promotion__span profile-promotion__span_sale'>Скидка 25%</span>}
                    <input
                      name='promotion'
                      type='radio'
                      value={option.value}
                      className='profile-promotion__input profile-promotion__input_invisible'
                      onClick={(e) =>{
                        if (e.target.value === promotionInSideBanners) {
                        setPromotionInSideBanners(null)
                        setPricePromotionInSideBanners(null)
                        } else {
                        setPromotionInSideBanners(e.target.value)
                        setPricePromotionInSideBanners(option.price)
                      }}}
                    />
                    <div className={`profile-promotion__input_visible ${promotionInSideBanners === option.value ? 'profile-promotion__input_checked' : 'profile-promotion__input_not-checked'}`}>
                      <h3 className='profile-promotion__input-title'>{option.label}</h3>
                      <p className='profile-promotion__input-subtitle'>{`${option.price} каст`}</p>
                    </div>
                  </label>
                )}
              </div>
            </div>
            <div className='profile-promotion__payment-container'>
              <BalanceInfo toPay={sumToPay}/>
              <button className='profile-promotion__form-button'>Оплатить</button>
            </div>
          </form>
        </main>
      <Footer />
    </>
  )
}

export default ProfilePromotion;