import './MainInfo.css';
import { useEffect } from 'react';
import GeneralInfoActor from '../../../../components/GeneralInfoActor/GeneralInfoActor';
import { useForm } from "react-hook-form";

function MainInfo({ handleButtonNextClick }) {
  const { 
    register, handleSubmit, formState: { errors }, control, setValue, watch
  } = useForm({
    mode: "onChange",
    defaultValues: {
      firstname: '',
      lastname: '',
      middlename: '',
      dateOfBirth: '',
      address: '',
      number: '',
      education: '',
      passport: '',
      readyToMove: ''
    }
  });

  const userInfo = JSON.parse(localStorage.getItem("mainUserInfo"));
  
  useEffect(() => {
    if(localStorage.getItem('mainUserInfo') !== null) {
      setValue('firstname', userInfo.firstname)
      setValue('lastname', userInfo.lastname)
      setValue('middlename', userInfo.middlename)
      setValue('dateOfBirth', userInfo.dateOfBirth)
      setValue('address', userInfo.address)
      setValue('number', userInfo.number)
      setValue('education', userInfo.education)
      setValue('passport', userInfo.passport)
      setValue('readyToMove', userInfo.readyToMove)
    }}, [])

  const onSubmit = (data) => {
    console.log(data)
    localStorage.setItem('mainUserInfo', JSON.stringify(data));
    handleButtonNextClick()
  }
    
  return (
    <>
      <form className='questionary' onSubmit={handleSubmit(onSubmit)}>
        <h2 className='questionary__title'>Общие данные</h2>
        <p className='questionary__subtitle'>Шаг 1 из 4</p>
        <div className='questionary__steps'>
          <span className='questionary__step questionary__step_passed'/>
          <span className='questionary__step'/>
          <span className='questionary__step'/>
          <span className='questionary__step'/>
        </div>
        <GeneralInfoActor register={register} control={control} errors={errors} setValue={setValue} watch={watch}/>
        <div className='question-list__buttons question-list__buttons_step_first'>
          <button className='question-list__button question-list__button_next' type='submit'>Далее</button>
        </div>
      </form>
    </>
  )
}

export default MainInfo;