import './Actors.css';
import { useState } from 'react';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import SearchForm from '../../components/SearchForm/SearchForm';
import Filter from './Filter/Filter';
import PrivelegedActors from '../../components/PrivelegedActors/PrivelegedActors';
import Promo from '../../components/Promo/Promo';
import ActorsTable from './ActorsTable/ActorsTable';
import useScreenWidth from "../../utils/useScreenWidth";

function Actors({ loggedIn }) {
  const size =  useScreenWidth();
  const [isOpenFilter, setIsOpenFilter] = useState(false);
  const [isDirty, setIsDirty] = useState(false);

  function handleButtonClick() {
    setIsOpenFilter(!isOpenFilter)
  };

  function closeFilter() {
    setIsOpenFilter(false)
  }

  const filterClassName = (
    `${isOpenFilter ? 'filter' : 'filter_invisible'}`
  )

  return (
    <>
      <Header loggedIn={loggedIn}/>
      <main className="content content_actors">
        <div className='actors__container'>
          <div className='actors__heading'>
            <h1 className='actors__title'>Актеры</h1>
            <p className='actors__subtitle'>Всего 549</p>
          </div>
          <SearchForm onButtonClick={handleButtonClick} isOpenFilter={isOpenFilter} placeholder='Поиск актера' isDirty={isDirty}/>
        </div>
        <Filter filterClassName={filterClassName} onClose={closeFilter} setIsDirty={setIsDirty}/>
        <PrivelegedActors />
        <ActorsTable />
        {
          size.width > 1620 ?
          <Promo />
          : ''
        }
      </main>
      <Footer />
    </>
  );
}

export default Actors;