import './BodyInfo.css';
import { useForm } from "react-hook-form";
import { useEffect } from 'react';
import BodyInfoInputs from '../../../../components/BodyInfoInputs/BodyInfoInputs';

function BodyInfo({ handleButtonBackClick, handleButtonNextClick }) {
  const { 
    register, handleSubmit, formState: { errors }, control, setValue
  } = useForm({
    mode: "onChange",
    defaultValues: {
      waist: '',
      hips: '',
      body: '',
      skin: '',
      bodyHair: '',
      looks: '',
      hairLength: '',
      hairForm: ''
    }
  });

  const bodyUserInfo = JSON.parse(localStorage.getItem("bodynUserInfo"));

  useEffect(() => {
    if(localStorage.getItem('bodynUserInfo') !== null) {
    setValue('gender', bodyUserInfo.gender)
    setValue('waist', bodyUserInfo.waist)
    setValue('hips', bodyUserInfo.hips)
    setValue('body', bodyUserInfo.body)
    setValue('skin', bodyUserInfo.skin)
    setValue('bodyHair', bodyUserInfo.bodyHair)
    setValue('looks', bodyUserInfo.looks)
    setValue('hairLength', bodyUserInfo.hairLength)
    setValue('hairForm', bodyUserInfo.hairForm)
  }}, [])

  const onSubmit = (data) => {
    localStorage.setItem('bodynUserInfo', JSON.stringify(data));
    console.log(data)
    handleButtonNextClick()
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <div className='questionary questionary_second-step'>
        <h2 className='questionary__title'>Телосложение</h2>
        <p className='questionary__subtitle'>Шаг 2 из 4</p>
        <div className='questionary__steps'>
          <span className='questionary__step questionary__step_passed'/>
          <span className='questionary__step questionary__step_passed'/>
          <span className='questionary__step'/>
          <span className='questionary__step'/>
        </div>
        <BodyInfoInputs register={register} control={control} errors={errors} />
      </div>
      <div className='question-list__buttons'>
        <button className='question-list__button question-list__button_back' onClick={handleButtonBackClick} type='button'>Назад</button>
        <button className='question-list__button question-list__button_next' type='submit'>Далее</button>
      </div>
    </form>
  )
}

export default BodyInfo;