import TextInput from '../TextInput/TextInput';
import './NewCastingInputs.css';
import { REQUIRED_ERROR, SUM_ERROR } from '../../constants/errors';
import TextareaInput from '../TextareaInput/TextareaInput';
import CheckboxFilter from '../CheckboxFilter/CheckboxFilter';
import SelectFilter from '../SelectFilter/SelectFilter';
import { selectOptions, GENDER_OPTIONS } from '../../constants/filterOptions';
import RadioFilter from '../RadioFilter/RadioFilter';
import { Controller } from 'react-hook-form';
import DateTimePicker from '../DateTimePicker/DateTimePicker'

function NewCastingInputs({ register, control, errors }) {

  return (
    <div className='casting-info__container'>
      <div className='casting-info__price-block'>
        <TextInput 
          title={'Сумма контракта в кастах'}
          placeholder={'Введите значение'}
          nameInput={'price'}
          errorValue={REQUIRED_ERROR}
          type='number'
          register={register}
          minValue = '1'
          message={SUM_ERROR}
          pattern={/^[1-9]/}
        />
        <span className='casting-info__ratio'>Курс: 1 каст = 1 рубль</span>
        <CheckboxFilter
          register={register}
          title={'Использовать Гарант'}
          checkBoxClassName={'filter-checkbox_use-grant'}
          link={true}
          name={'useGrant'}
        />
        {errors.price && (
        <div className='input__error'>
          {errors.price.message}
        </div>
        )}
      </div>
      <TextInput 
        title={'Заголовок задания'}
        placeholder={'Введите название'}
        nameInput={'custingTitle'}
        errorValue={REQUIRED_ERROR}
        type='text'
        register={register}
        errors={errors}
      />
      <TextInput 
        title={'Ссылка на гул-форму'}
        placeholder={'Прикрепите ссылку'}
        nameInput={'link'}
        errorValue={REQUIRED_ERROR}
        type='text'
        register={register}
        errors={errors}
      />
      <TextareaInput
        title={'Описание требуемого актёра, его роли и специфики работы'}
        placeholder={'Чем вы любите заниматься'}
        nameInput={'description'}
        errorValue={REQUIRED_ERROR}
        register={register}
        errors={errors}
      />
      <div className='casting-info__place-block'>
        <label className='input__title '>
          Дата и время
          <Controller
            name="date"
            control={control}
            rules={{
              required: REQUIRED_ERROR,
            }}
            render={
            ({ field }) => (
              <DateTimePicker
              {...field}
              />
            )}
          />
          {errors.date &&
            <div className='input__error'>
              {REQUIRED_ERROR}
            </div>
          }
        </label>
        <SelectFilter options={selectOptions} title={'Город'} control={control} nameInput={'city'} rules={{required: REQUIRED_ERROR}}/>
        <div className='casting-info__includes-options'>
          <h3 className='casting-info__question-title'>Дата и время</h3>
          <CheckboxFilter register={register} title={'Оплата проезда'} name={'transportPayment'}/>
          <CheckboxFilter register={register} title={'Оплата проживания'} name={'accommodationPayment'}/>
          <CheckboxFilter register={register} title={'Оплата питания'} name={'mealsPayment'}/>
        </div>
      </div>
      <div className='casting-info__gender-block'>
        <h3 className='casting-info__question-title'>Пол</h3>
        <RadioFilter options={[GENDER_OPTIONS[1], GENDER_OPTIONS[2]]} register={register} nameInput={'gender'} errors={errors} errorValue={REQUIRED_ERROR}/>
      </div>
    </div>

  )
}

export default NewCastingInputs;