import './FirstCastingInfo.css';
import { useState } from 'react';
import NewCastingInputs from '../../../../components/NewCastingInputs/NewCastingInputs';
import Popup from '../Popup/Popup'

import { useForm } from "react-hook-form";

function FirstCastingInfo({  handleButtonBackClick }) {
  const { 
    register, handleSubmit, formState: { errors }, control, setValue, watch
  } = useForm({
    mode: "onChange",
    defaultValues: {
      
    }
  });

  const [isPopupOpen, setIsPopupOpen] = useState(false);
  const [isAgree, setIsAgree] = useState(false)

  const closePopup = () => {
    setIsPopupOpen(false)
  }

  const handleButtonAgree = () => {
    setIsAgree(true)
  }

  const onSubmit = (data) => {
    console.log(data)
    setIsPopupOpen(true)
  }
    
  return (
    <>
      <form className='questionary' onSubmit={handleSubmit(onSubmit)}>
        <NewCastingInputs register={register} control={control} errors={errors} watch={watch} />
        <div className='question-list__buttons'>
          <button className='question-list__button question-list__button_back' onClick={handleButtonBackClick} type='button'>Назад</button>
          <div className='question-list__buttons_next'>
            <button className='question-list__button question-list__button_pass-step' onClick={() => setIsPopupOpen(true)}>Пропустить шаг</button>
            <button className='question-list__button question-list__button_next' type='submit'>Далее</button>
          </div>
        </div>
      </form>
      <Popup onClose={closePopup} isOpen={isPopupOpen} onAgree={handleButtonAgree}/>
    </>
  )
}

export default FirstCastingInfo;