import './MenuProfile.css';
import { NavLink, Link } from 'react-router-dom';
import profileIcon from '../../../images/profile-icon.svg';
import noteIcon from '../../../images/note-icon.svg';
import castingsIcon from '../../../images/my-casting-icon.svg';
import advanceIcon from '../../../images/advance-icon.svg';
import Footer from '../../Footer/Footer';
import { ROUTES } from '../../../constants/routes';
import { useEffect } from 'react'

function MenuProfile({ menuProfileClassName, handleProfileClick, isMenuProfileOpen, size, handleNoticeClick, onClose, userType }) {
  const  handleContainerClick = (e) => {
    const container = document.querySelector('.menu-profile');
    if (container && !container.contains(e.target)) {
      onClose()
    }
  }
    useEffect(() => {
      document.addEventListener('mousedown', handleContainerClick)
  }, []);
   
  return (
    <nav className={menuProfileClassName}>
      <button className='menu-profile__button menu-profile__button_close' onClick={handleProfileClick}/>
      <ul className='menu-profile__list'>
        <li className='menu-profile__item'>
          <NavLink exact to={ROUTES.PROFILE} className='menu-profile__link' activeClassName='menu-profile__link_active'>
            <img className='menu-profile__icon' src={profileIcon} alt='Профиль'/>
            <p className='menu-profile__text'>Профиль</p>
            <span className='menu-profile__notice_invisible'></span>
          </NavLink>
        </li>
        <li className='menu-profile__item menu-profile__item_notifications'>
          <button className='menu-profile__link menu-profile__link_notifications' onClick={handleNoticeClick}>
            <img className='menu-profile__icon' src={noteIcon} alt='Уведомления'/>
            <p className='menu-profile__text'>Уведомления</p>
            <span className='menu-profile__notice'></span>
          </button>
        </li>
        <li className='menu-profile__item'>
          <NavLink exact to={ROUTES.MY_CASTINGS} className='menu-profile__link' activeClassName='menu-profile__link_active'>
            <img className='menu-profile__icon' src={castingsIcon} alt='Мои кастинги'/>
            <p className='menu-profile__text'>Мои кастинги</p>
            <span className='menu-profile__notice'></span>
          </NavLink>
        </li>
        <li className='menu-profile__item'>
          <NavLink exact to={ROUTES.PROFILE_PROMOTION} className='menu-profile__link' activeClassName='menu-profile__link_active'>
            <img className='menu-profile__icon' src={advanceIcon} alt='Продвижение'/>
            <p className='menu-profile__text'>Продвижение</p>
            <span className='menu-profile__notice_invisible'></span>
          </NavLink>
        </li>
      </ul>
      {userType === 'customer' && size.width<995 && <Link to={ROUTES.NEW_CASTING} className='menu-profile__button_new-cast'>Новый кастинг</Link>}
      <button className='menu-profile__button menu-profile__button_exit'>Выйти из профиля</button>
      {size.width<769 && <Footer footerClassName={'footer_profile-menu'}/>}
    </nav>
  )
}

export default MenuProfile;