import './ActorProfile.css';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import MainInfo from './MainInfo/MainInfo';
import CastingsInfo from '../../components/CastingsInfo/CastingsInfo';
import GeneralInfo from './GeneralInfo/GeneralInfo';
import { NavLink } from 'react-router-dom';
import { ROUTES } from '../../constants/routes';
import PopupSlider from './PopupSlider/PopupSlider';
import { useState } from 'react';

function ActorProfile({ loggedIn }) {
  const [isVerificated, setIsVerificated] = useState(false)
  const [isPopupOpen, setIsPopupOpen] = useState(false);
  const [initialPhoto, setInitialPhoto] = useState({})

  function closePopup() {
    setIsPopupOpen(false)
  }

  function openPopup() {
    setIsPopupOpen(true)
  }

  return (
    <>
      <Header loggedIn={loggedIn}/>
      <main className='content content_actor-profile profile'>
        <h1 className='profile__title'>Профиль</h1>
        <div className='profile__buttons'>
          <NavLink exact to={ROUTES.EDIT_PROFILE} className='profile__button profile__button_edit'>
            <span className='profile__button-icon'/>
            <p className='profile__button-text'>Редактировать</p>
          </NavLink>
          {!isVerificated &&
          <NavLink exact to={ROUTES.PROFILE_VERIFICATION} className='profile__button profile__button_verification'>
            <p className='profile__button-text'>Пройти верификацию</p>
          </NavLink>
          }
        </div>
        <MainInfo openPopup={openPopup} setInitialPhoto={setInitialPhoto} isVerificated={isVerificated}/>
        <CastingsInfo />
        <GeneralInfo />
      </main>
      <Footer />
      <PopupSlider onClose={closePopup} isOpen={isPopupOpen} photo={initialPhoto}/>
    </>
  )
}

export default ActorProfile;