import './CastingsTable.css';
import Banner from '../../../components/Banner/Banner';
import CastingItem from '../../../components/CastingItem/CastingItem';
import { CASTINGS } from '../../../constants/const';
import useScreenWidth from '../../../utils/useScreenWidth';
import PromoCasting from '../../../components/Promo/PromoCasting/PromoCasting';

function CastingsTable() {
  const size =  useScreenWidth();

  return (
    <section className='castings'>
      <ul className='castings-table'>
        {CASTINGS.slice(0, CASTINGS.length).map((casting) =>
          (<li className='castings-table__item' key={casting.id}>
            <CastingItem casting={casting}/>
          </li>)
        )}
        <li className='castings-table__item_banner'>
        {
          size.width > 992 ?
          <Banner />
          :
          <PromoCasting 
            city={'Москва'}
            price={'5 000'}
            title={'Нужны с опытом в сериале. Царские времена. Девушки.'}
            tags={['Кинематограф', 'Массовка']}
          />
        }
        </li>
      </ul>
      <button className='castings-table__button'>Показать еще</button>
    </section>
  )
}

export default CastingsTable;