import './BalanceInfo.css';
import { Link } from 'react-router-dom';
import { ROUTES } from '../../constants/routes';

function BalanceInfo({ toPay }) {
  const regex = /^[1-9]/

  return (
    <ul className='balance-container'>
      <li className='balance-container__balance'>
        <h3 className='balance-container__title'>Ваш баланс</h3>
        <p className='balance-container__value balance-container__value_balance'>1 500 каст</p>
        <Link to={ROUTES.FINANCE} className='balance-container__link'>Пополнить</Link>
      </li>
      <li className='profile-promotion__payment'>
        <h3 className='balance-container__title'>К оплате</h3>
        <p className='balance-container__value'>{Boolean(toPay.match(regex)) ? `${toPay} каст` : '0 каст'}</p>
      </li>
    </ul>
  )
}

export default BalanceInfo;