import './Header.css';
import { useState, useContext } from 'react';
import { NavLink, Link } from 'react-router-dom';
import HeaderLogo from '../HeaderLogo/HeaderLogo';
import useScreenWidth from "../../utils/useScreenWidth";
import MenuProfile from './MenuProfile/MenuProfile';
import Notifications from './Notifications/Notifications';
import { ROUTES } from '../../constants/routes';
import { CurrentUserContext } from '../../contexts/CurrentUserContext';
import MobileMenu from './MobileMenu/MobileMenu';
import profilePhoto from '../../images/Photo/header-photo.png';
import photoIcon from '../../images/header-photo-icon.png'

function Header({ loggedIn }) {
  const size =  useScreenWidth();
  const userType = useContext(CurrentUserContext);

  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isMenuProfileOpen, setIsMenuProfileOpen] = useState(false);
  const [isPopupNoticeOpen, setIsPopupNoticeOpen] = useState(false);

  const mobileMenuClassName = (
    `${isMobileMenuOpen ? 'mobile-menu' : 'mobile-menu_invisible'}`
  );

  const menuProfileClassName = (
    `${isMenuProfileOpen ? 'menu-profile' : 'menu-profile_invisible'}`
  );

  const notificationsListClassName = (
    `${isPopupNoticeOpen ? 'notices notices_opened ' : 'notices'}`
  );

  function handleProfileClick() {
    setIsMenuProfileOpen(!isMenuProfileOpen)
  }

  function closeMenuProfile() {
    setIsMenuProfileOpen(false)
  }

  function openMobileMebu() {
    setIsMobileMenuOpen(true)
  }

  function closeMobileMenu() {
    setIsMobileMenuOpen(false)
  }

  function handleNoticeClick() {
    setIsPopupNoticeOpen(true)
  }

  function closeNoticesList() {
    setIsPopupNoticeOpen(false)
  }

  return (
    <header className='header'>
      <div className='header__wrapper'>
        {size.width>768 ?
          ''
          : <button className='heder__mobile-menu' onClick={openMobileMebu}/>}
        <div className='header__container'>
          <HeaderLogo></HeaderLogo>
          {size.width>768 ? 
            <nav className='header__links'>
              <NavLink exact to={ROUTES.ACTORS} className='header__link' activeClassName='header__link_active'>Актеры</NavLink>
              <NavLink exact to={ROUTES.CASTINGS} className='header__link' activeClassName='header__link_active'>Кастинги</NavLink>
              <NavLink exact to={ROUTES.ABOUT} className='header__link' activeClassName='header__link_active'>О нас</NavLink>
            </nav>
            : ''
          }
        </div>
        {loggedIn ?
          <>
            <div className='header__actor-profile-block'>
              <button className='header__bell' onClick={handleNoticeClick}/>
              <div className='header__actor-profile' onClick={handleProfileClick}>
                <h2 className='header__actor-profile-name'>Александра Румянцева</h2>
                <p className='header__actor-profile-email'>Sasha88@gmail.com</p>
                <img className='header__actor-profile-photo' src={profilePhoto ? profilePhoto : photoIcon} alt='Фото Актера' />
                <span className='header__notice'></span>
                <MenuProfile
                  menuProfileClassName={menuProfileClassName}
                  handleProfileClick={handleProfileClick}
                  isMenuProfileOpen={isMenuProfileOpen}
                  size={size}
                  handleNoticeClick={handleNoticeClick}
                  onClose={closeMenuProfile}
                  userType={userType}
                  />
              </div>
              {userType === 'customer' && <Link to={ROUTES.NEW_CASTING} className='header__new-cast'>Новый кастинг</Link>}
            </div>
            <Notifications notificationsClassName={notificationsListClassName} onClose={closeNoticesList}/>
          </>
          :
          <NavLink exact to={ROUTES.LOGIN} className='header__link header__link_type_signin'>
            <p className='header__link-text'>
              Войти
            </p>
            <div className='header__link-img'></div>
          </NavLink>
        }
      </div>
      <MobileMenu
        className={mobileMenuClassName}
        onClose={closeMobileMenu}
        size={size}
      />
    </header>
  )
}

export default Header;