import './PrivelegedActors.css';
import photo from '../../images/Photo/1.png';
import photo2 from '../../images/Photo/2.png';
import PrivelegedActor from '../PrivelegedActor/PrivelegedActor';
import useScreenWidth from "../../utils/useScreenWidth";

function PrivelegedActors() {
  const size =  useScreenWidth();

  const sectionClassName = (
    `${(size.width>992) ? 'priveleged-actors' : 'priveleged-actors_disable'}`
  )

  return (
    <section className={sectionClassName}>
      <ul className='priveleged-actors__list'>
        <PrivelegedActor photo={photo}/>
        <PrivelegedActor photo={photo2}/>
      </ul>
      <p className='priveleged-actors__question'>Как сюда попасть?</p>
    </section>
  );
}

export default PrivelegedActors;