import './PopupSlider.css';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import { photos } from '../../../constants/const';

function SampleNextArrow(props) {
  const { style, onClick } = props;
  return (
    <div
      className='popup-slider__arrow popup-slider__arrow_next'
      style={{ ...style}}
      onClick={onClick}
    />
  );
}

function SamplePrevArrow(props) {
  const { style, onClick } = props;
  return (
    <div
    className='popup-slider__arrow popup-slider__arrow_prev'
      style={{ ...style }}
      onClick={onClick}
    />
  );
}

export default function PopupSlider({photo, isOpen, onClose}) {
    let photosArr = []
    photosArr = photosArr.concat(photos.slice(`${photo.id-1}`), photos)

    const settings = {
      className: 'popup-slider',
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      nextArrow: <SampleNextArrow />,
      prevArrow: <SamplePrevArrow />,
    };

  return (
    <div
      onClick={e => (e.currentTarget === e.target) && onClose()}
      className={`popup-photos ${isOpen && "popup-photos_opened"}`}
    >
      <div className="popup-photos__container">
        <button className='popup-photos__button_close' onClick={onClose}/>
        <Slider {...settings} >
          {photosArr.map((photo, index) =>
            <div key={photo.id} className='popup-slider__container'>
              <img src={photo.src} alt={photo.title} className='popup-slider__image'></img>
            </div>
          )}
        </Slider>
      </div>
    </div>
  )
}
