import Notification from './Notification/Notification';
import './Notifications.css';

function Notifications({ notificationsClassName, onClose}) {
  const notices = [
    {
      id: 1,
      date: '12.12.22  09:45',
      text: 'Вас выбрали для ',
      new: 'true',
      link: '/'
    },
    {
      id: 2,
      date: '12.12.22  09:45',
      text: 'Вас выбрали для ',
      new: 'false',
      link: '/'
    },
    {
      id: 3,
      date: '12.12.22  09:45',
      text: 'Вас выбрали для ',
      new: 'false',
      link: '/'
    },
  ]

  return (
    <div
      onClick={e => (e.currentTarget === e.target) && onClose()}
      className={notificationsClassName}
      >
      <div className='notices__container'>
        <h2 className='notices__title'>Уведомления</h2>
        <button className='notices__button-close' onClick={onClose}/>
        <ul className='notices__list'>
        {notices.slice(0, notices.length).map((notice) =>
        (<Notification date={notice.date} key={notice.id} text={notice.text} newNotice={notice.new} link={notice.link}/>)
        )}
        </ul>
      </div>
    </div>
  )
}

export default Notifications;