import './MultiSelectFilter.css';
import Select from 'react-select';
import { Controller } from 'react-hook-form';

function MultiSelectFilter({ options, title, control, nameInput, placeholder }) {
  return (
    <label className='custom-select__label'>
      {title}
      <Controller
      control={control}
      name={nameInput}
      render={({ field: {onChange} }) => (
      <>
        <Select
          classNamePrefix='multi-select'
          placeholder={placeholder}
          isMulti={true}
          isSearchable={true}
          onChange={onChange}
          Drop
          options={options}
          formatOptionLabel={actor => (
            <div className='multi-select__option-container'>
              <p className='multi-select__option-text'>{actor.label}</p>
              <img src={actor.image} alt={actor.label} className='multi-select__option-image'/>
            </div>
          )}
        />
      </>
      )}
      />
    </label>
  )
}

export default MultiSelectFilter;