import './GeneralInfoActor.css'
import { useState, useEffect } from 'react';
import SocialNetworks from '../SocialNetworks/SocialNetworks';
import Switch from '../Switch/Switch';
import DatePicker from '../DatePicker/DatePicker';
import { REQUIRED_ERROR, INAVID_NUMBER_ERROR } from '../../constants/errors'
import { Controller } from "react-hook-form";
import TextInput from '../TextInput/TextInput'

function GeneralInfoActor({ generalInfoClassName, register, control, errors, watch }) {

  const [isNumberInputGetValue, setIsNumberInputGetValue] = useState(false);

  const numberPlaceholderClassName = (
    `${isNumberInputGetValue ? 'input__placeholder' : 'input__placeholder input__placeholder_nonfocus'}`
  );

 const questionsClassName = (
    `${generalInfoClassName ? generalInfoClassName : 'questionary__questions questionary__questions_step_first'}`
  );

  const watchNumber = watch('number');

  useEffect(() => {
    if(watchNumber === '') {
      setIsNumberInputGetValue(false)
    } else {setIsNumberInputGetValue(true)}
  }, [watchNumber])

  return (
    <div className={questionsClassName}>
      <div className='questionary__container questionary__container_general-info'>
        <TextInput
          title={'Имя'}
          placeholder={'Ведите свое имя'}
          nameInput={'firstname'}
          errorValue={REQUIRED_ERROR}
          type='text'
          register={register}
          errors={errors}
          />
        <TextInput
          title={'Фамилия'}
          placeholder={'Ведите свою фамилию'}
          nameInput={'lastname'}
          errorValue={REQUIRED_ERROR}
          type='text'
          register={register}
          errors={errors}
          />
        <TextInput
          title={'Отчество'}
          placeholder={'Ведите свое отчество'}
          nameInput={'middlename'}
          errorValue={REQUIRED_ERROR}
          type='text'
          register={register}
          errors={errors}
          />
        <label className='input__title '>
          Дата рождения
          <Controller
            name="dateOfBirth"
            control={control}
            rules={{
              required: REQUIRED_ERROR,
            }}
            render={
            ({ field }) => (
              <DatePicker
              {...field}
              />
            )}
          />
          {errors.dateOfBirth &&
            <div className='input__error input__error_calendar'>
              {REQUIRED_ERROR}
            </div>
          }
        </label>
      </div>
      <div className='questionary__container questionary__container_contacts'>
        <TextInput
          title={'Адрес'}
          placeholder={'Введите адрес'}
          nameInput={'address'}
          errorValue={REQUIRED_ERROR}
          type='text'
          register={register}
          errors={errors}
          />
        <label className='input__title'>
          Номер телефона
          <span className={numberPlaceholderClassName}>+7</span>
          <input
            className='input input_number'
            type='number'
            {...register('number', {
              required: REQUIRED_ERROR,
              pattern: {
                value: /^\d{10}$/,
                message: INAVID_NUMBER_ERROR
              }
            })}
          />
          {errors.number && (
            <div className='input__error'>
              {errors.number.message}
            </div>
          )}
        </label>
      </div>
      <div className='questionary__container questionary__container_education'>
        <TextInput
          title={'Образование'}
          placeholder={'Учебное заведение, факультет, специальност'}
          nameInput={'education'}
          errorValue={REQUIRED_ERROR}
          type='text'
          register={register}
          errors={errors}
          />
        <SocialNetworks />
      </div>
      <div className='questionary__switches'>
        <label className="switch">
          <input
          type="checkbox"
          {...register('passport')}
          className="switch_type_invisible"/>
          <span className="switch_type_visible"/>
          <span className="switch__text">Есть загран. паспорт</span>
        </label>
        <label className="switch">
          <input
          type="checkbox"
          {...register('readyToMove')}
          className="switch_type_invisible"/>
          <span className="switch_type_visible"/>
          <span className="switch__text">Готов работать в другом городе/стран</span>
        </label> 
      </div>
    </div>
  )
}

export default GeneralInfoActor;
