import './Filter.css';
import { useState, useEffect } from 'react'
import { useForm } from 'react-hook-form';
import SelectFilter from '../../../components/SelectFilter/SelectFilter';
import RadioFilter from '../../../components/RadioFilter/RadioFilter';
import RangeSlider from '../../../components/RangeSlider/RangeSlider';
import TextInput from '../../../components/TextInput/TextInput';
import useScreenWidth from "../../../utils/useScreenWidth";
import { 
  selectOptions,
  BODY_TIPE_OPTIONS,
  SKIN_TIPE_OPTIONS,
  BODY_HAIR_OPTIONS,
  LOOKS_TYPE_OPTIONS,
  GENDER_OPTIONS,
  HAIR_LENGTH_OPTIONS,
  THEME_OPTIONS,
  SMOKING_ATTITUDE_OPTIONS,
  ALCOHOL_ATTITUDE_OPTIONS,
  PSYCHOTROPIC_SUBSTANCES_ATTITUDE_OPTIONS,
  ORIENTATION_TYPE_OPTIONS,
  MARITAL_STATUS_OPTIONS,
  CHILDREN_STATUS_OPTIONS
} from '../../../constants/filterOptions'

function Filter({ filterClassName, onClose, setIsDirty }) {
  const size =  useScreenWidth();

  const { 
    register, handleSubmit, formState: { errors, dirtyFields, isDirty }, control, setValue, watch, reset
  } = useForm({
    mode: "onChange",
    defaultValues: {
      theme: '',
      minAge: '',
      maxAge: '',
      city: '',
      gender: '',
      waist: '',
      hips: '',
      body: '',
      skin: '',
      bodyHair: '',
      looks: '',
      hairLength: '',
      smokingAttitude: '',
      alcoholAttitude: '',
      psychotropicSubstancesAttitude: '',
      orientation: '',
      maritalStatus: '',
      childrens: ''
    }
  });

  const [isOpenAdvacedFilter, setIsOpenAdvacedFilter] = useState(false);
  const [isOpenBodyTipeFilter, setIsOpenBodyTipeFilter] = useState(false);
  const [isOpenRelationsFilter, setIsOpenRelationsFilter] = useState(false);

  function handleButtonViewMoreFiltersClick() {
    setIsOpenAdvacedFilter(!isOpenAdvacedFilter)
  };

  function handleButtonBodyTipeFiltersClick() {
    setIsOpenBodyTipeFilter(!isOpenBodyTipeFilter)
  };

  function handleButtonRelationsFiltersClick() {
    setIsOpenRelationsFilter(!isOpenRelationsFilter)
  };

  // const  handleContainerClick = (e) => {
  //   const container = document.querySelector('.filter');
  //   if (container && !container.contains(e.target)) {
  //     onClose()
  //   }
  // }
  //   useEffect(() => {
  //   if (size.width < 768) {
  //     document.addEventListener('mousedown', handleContainerClick)
  //   }
  // }, [size]);


  const advancedFilterClassName = (
    `${isOpenAdvacedFilter ? 'filter_advanced' : 'filter_advanced-invisible'}`
  )

  const buttonArrowAdvacedFilterClassName = (
    `${isOpenAdvacedFilter ? 'filter__button-arrow filter__button-arrow_filter-open' : 'filter__button-arrow'}`
  )

  const bodyTipeFilterClassName = (
    `${isOpenBodyTipeFilter ? 'filter__body-tipe' : 'filter__body-tipe_invisible'}`
  )

  const buttonArrowBodyTipeFilterClassName = (
    `${isOpenBodyTipeFilter ? 'filter__button-arrow filter__button-arrow_filter-open' : 'filter__button-arrow'}`
  )

  const relationsFilterClassName = (
    `${isOpenRelationsFilter ? 'filter__relations' : 'filter__relations_invisible'}`
  )

  const buttonArrowRelationsFilterClassName = (
    `${isOpenRelationsFilter ? 'filter__button-arrow filter__button-arrow_filter-open' : 'filter__button-arrow'}`
  )
  useEffect(() => {
    if(isDirty) {
      setIsDirty(true)
    } else {setIsDirty(false)}
  }, [isDirty])

  const onSubmit = (data) => {
    onClose()
  }

  return (
    <form className={filterClassName} onSubmit={handleSubmit(onSubmit)}>
      <div className='filter_non-advanced'>
        <div className='filter__items'>
          <SelectFilter options={THEME_OPTIONS} title={'Тематика'} control={control} nameInput={'theme'}/>
          <SelectFilter options={selectOptions} title={'Город'} control={control} nameInput={'city'}/>
          <RadioFilter options={GENDER_OPTIONS} title={'Пол'} register={register} nameInput={'gender'}/>
          <RangeSlider register={register} errors={errors}/>
        </div>
        <div className={advancedFilterClassName}>
          {/* {size.width > 788 ? 
          <>
          <div className='filter__items'>
            <Select options={selectOptions} title={'Тематика'}/>
            <Select options={selectOptions} title={'Город'}/>
            <RadioFilter options={GENDER_OPTIONS} title={'Пол'}/>
            <RangeSlider options={GENDER_OPTIONS}/>
          </div>
          <div className='filter__items filter__items_with-border'>
            <Select options={selectOptions} title={'Тематика'}/>
            <Select options={selectOptions} title={'Город'}/>
            <RadioFilter options={GENDER_OPTIONS} title={'Пол'}/>
            <RangeSlider />
          </div>
          </>
          : '' } */}
          <div className='filter__container filter__container_body'>
            <button className='filter__container-button' onClick={handleButtonBodyTipeFiltersClick} type='button'>
              <p className='filter__container-title'>Телосложение</p>
              <span className={buttonArrowBodyTipeFilterClassName}></span>
            </button>
            <div className={bodyTipeFilterClassName}>
              <RadioFilter options={[GENDER_OPTIONS[1], GENDER_OPTIONS[2]]} title={'Пол'} register={register} nameInput={'gender'}/>
              <div className='filter__items'>
                <TextInput title={'Объем талии, см'} placeholder={'Введите значение'} nameInput={'waist'} register={register} type={'number'}/>
                <TextInput title={'Объем бедер, см'} placeholder={'Введите значение'} nameInput={'hips'} register={register} type={'number'}/>
                <SelectFilter options={BODY_TIPE_OPTIONS} title={'Телосложение'} control={control} nameInput={'body'}/>
                <SelectFilter options={SKIN_TIPE_OPTIONS} title={'Цвет кожи'} control={control} nameInput={'skin'}/>
              </div>
              <div className='filter__items'>
                <SelectFilter options={BODY_HAIR_OPTIONS} title={'Наличие волос на теле'} control={control} nameInput={'bodyHair'}/>
                <SelectFilter options={LOOKS_TYPE_OPTIONS} title={'Внешность'} control={control} nameInput={'looks'}/>
                <SelectFilter options={HAIR_LENGTH_OPTIONS} title={'Длина волос'} control={control} nameInput={'hairLength'}/>
                <SelectFilter options={SKIN_TIPE_OPTIONS} title={'Цвет кожи'} control={control} nameInput={'skin'}/>
              </div>
            </div>
          </div>
          <div className='filter__container filter__container_relations'>
            <button className='filter__container-button' onClick={handleButtonRelationsFiltersClick} type='button'>
              <p className='filter__container-title'>Отношения и интересы </p>
              <span className={buttonArrowRelationsFilterClassName}></span>
            </button>
            <div className={relationsFilterClassName}>
              <div className='questionary__questions questionary__questions_step_third'>
                <div className='questionary__container questionary__container_attitude'>
                  <SelectFilter options={SMOKING_ATTITUDE_OPTIONS} title={'Отношение к курению'} control={control} nameInput={'smokingAttitude'}/>
                  <SelectFilter options={ALCOHOL_ATTITUDE_OPTIONS} title={'Отношение к алкоголю'} control={control} nameInput={'alcoholAttitude'}/>
                  <SelectFilter options={PSYCHOTROPIC_SUBSTANCES_ATTITUDE_OPTIONS} title={'Отношение к психотропным веществам'} control={control} nameInput={'psychotropicSubstancesAttitude'}/>
                </div>
                <div className='questionary__container questionary__container_interests'>
                  <SelectFilter options={ORIENTATION_TYPE_OPTIONS} title={'Ориентация'} control={control} nameInput={'orientation'}/>
                  <SelectFilter options={MARITAL_STATUS_OPTIONS} title={'Семейное положение'} control={control} nameInput={'maritalStatus'}/>
                  <SelectFilter options={CHILDREN_STATUS_OPTIONS} title={'Наличие детей'} control={control} nameInput={'childrens'}/>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className='filter__buttons'>
          <button className='filter__button_view-more' type='button' onClick={handleButtonViewMoreFiltersClick}>
            <p className='filter__button-text'>Расширенный фильтр</p>
            <div className={buttonArrowAdvacedFilterClassName}></div>
            </button>
          <div className='filter__buttons-container'>
            <button className='filter__button_reset' type='reset' onClick={() => {reset()}}>Сбросить</button>
            <button className='filter__button_submitt' type='submit'>Применить</button>
          </div>
        </div>
      </div>
    </form>
  );
}

export default Filter;