import './InterestsInfo.css';
import { useForm } from "react-hook-form";
import { useEffect } from 'react';
import ThemeList from '../../../../components/ThemeList/ThemeList';
import { THEME_OPTIONS } from '../../../../constants/filterOptions'
import InterestsInputs from '../../../../components/InterestsInputs/InterestsInputs';

function InterestsInfo({ handleButtonBackClick, handleButtonNextClick }) {
  const { 
    register, handleSubmit, formState: { errors }, control, setValue
  } = useForm({
    mode: "onChange",
    defaultValues: {
      themes: '',
      smokingAttitude: '',
      alcoholAttitude: '',
      psychotropicSubstancesAttitude: '',
      orientation: '',
      maritalStatus: '',
      childrens: '',
      hobbies: '',
      about: ''
    }
  });

  const interestsUserInfo = JSON.parse(localStorage.getItem("interestsUserInfo"));

  useEffect(() => {
    if(localStorage.getItem('interestsUserInfo') !== null) {
      setValue('themes', interestsUserInfo.themes)
      setValue('smokingAttitude', interestsUserInfo.smokingAttitude)
      setValue('alcoholAttitude', interestsUserInfo.alcoholAttitude)
      setValue('psychotropicSubstancesAttitude', interestsUserInfo.psychotropicSubstancesAttitude)
      setValue('orientation', interestsUserInfo.orientation)
      setValue('maritalStatus', interestsUserInfo.maritalStatus)
      setValue('childrens', interestsUserInfo.childrens)
      setValue('hobbies', interestsUserInfo.hobbies)
      setValue('about', interestsUserInfo.about)
    }}, [])
    
  const onSubmit = (data) => {
    console.log(data)
    localStorage.setItem('interestsUserInfo', JSON.stringify(data));
    handleButtonNextClick()
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <div className='questionary questionary_third-step'>
        <h2 className='questionary__title'>Отношения и интересы</h2>
        <p className='questionary__subtitle'>Шаг 3 из 4</p>
        <div className='questionary__steps'>
          <span className='questionary__step questionary__step_passed'/>
          <span className='questionary__step questionary__step_passed'/>
          <span className='questionary__step questionary__step_passed'/>
          <span className='questionary__step'/>
        </div>
        <ThemeList options={THEME_OPTIONS} errors={errors} register={register}/>
        <InterestsInputs control={control} errors={errors} register={register}/>
      </div>
        <div className='question-list__buttons'>
        <button className='question-list__button question-list__button_back' onClick={handleButtonBackClick} type='button'>Назад</button>
        <button className='question-list__button question-list__button_next' type='submit'>Далее</button>
      </div>
    </form>
  )
}

export default InterestsInfo;