import './MainInfo.css';
import { useRef, useState } from 'react';

const tags = [
  'Кинематограф',
  'Театр',
  'Массовка'
];

function MainInfo({ isVerificated }) {
  const photoRef = useRef(0);
  const [isPhotoLoad, setIsPhotoLoad] = useState(false)

  const handleFileReader = (e, ref) => {
    ref.current.style.backgroundImage = "url(" + URL.createObjectURL(e.target.files[0]) + ")";
  }

  const handlePhotoLoad = (e) => {
    handleFileReader(e, photoRef)
    setIsPhotoLoad(true)
  }

  return (
    <section className='main-info main-info_customer'>
      {isVerificated ?
      <div className='main-info__verification'>
        <p className='main-info__verification-text'>Проверенный</p>
        <span className='main-info__verification-icon'/>
      </div>
      :
      <div className='main-info__not-verification'>
        <p className='main-info__not-verification-text'>Получите значок доверия</p>
        <span className='main-info__not-verification-icon'/>
      </div>
      }
      <div className='main-info__photo-container' ref={photoRef}>
        {!isPhotoLoad && <span className='main-info__photo-icon'/>}
        <input
        type='file'
        accept="image/*"
        className='main-info__photo-input_invisible'
        />
        <label className='main-info__photo-label'>
          <input
          type='file'
          accept="image/*"
          className='main-info__photo-input_invisible'
          onChange={(e) => {
            handlePhotoLoad(e);
          }}
          />
          <span className='main-info__photo-input_visible'/>
        </label>
      </div>
      <div className='main-info__container'>
        <div className='main-info__tags'>
          {tags.slice(0, tags.length).map((tag, index) => (
            <p className='main-info__tag' key={index}>{tag}</p>
          ))}
        </div>
        <div className='main-info__name-container'>
          <h2 className='main-info__name'>Никита Козловский</h2>
          <div className='main-info__star-container'>
            <span className='main-info__star-icon'/>
            <p className='main-info__star-value'>4.5</p>
          </div>
        </div>
        <div className='main-info__info-container'>
          <p className='main-info__info-container-title main-info__info-container-title_gender'>Москва</p>
        </div>
        <ul className='main-info__contacts'>
          <li className='main-info__contact'>
            <h3 className='main-info__contact-title'>Номер телефона</h3>
            <p className='main-info__contact-subtitle'>+7 987 654 32 10</p>
          </li>
          <li className='main-info__contact'>
            <h3 className='main-info__contact-title'>Email</h3>
            <p className='main-info__contact-subtitle'>sasha88@gmail.com</p>
          </li>
        </ul>
        <div className='main-info__about-container'>
          <div className='main-info__about-container-item main-info__about-container-item_about'>
            <h3 className='main-info__about-container-title main-info__about-container-title_about'>О себе</h3>
            <p className='main-info__about-container-subtitle main-info__about-container-subtitle_about'>
              Так уж заведено, что хвастовство в той или иной степени свойственно, пожалуй, каждому из нас. 
              Мы хвалимся новыми игрушками, потом школьными успехами, а дальше своими вторыми половинками и карьерами.
              Тем не менее, всегда нужно помнить, что «хвалиться — означает без всякой учтивости говорить другим: я лучше вас».
            </p>
          </div>
        </div>
      </div>
    </section>
  )
}

export default MainInfo;