import './GeneralInfoCustomer.css'
import { useState, useEffect } from 'react';
import { REQUIRED_ERROR, INAVID_NUMBER_ERROR } from '../../constants/errors'
import TextInput from '../TextInput/TextInput';
import RadioFilter from '../RadioFilter/RadioFilter'
import { LEGAL_STATUS_OPTIONS } from '../../constants/filterOptions';

function GeneralInfoCustomer({ register, control, errors, watch }) {
  const [isNumberInputGetValue, setIsNumberInputGetValue] = useState(false);

  const numberPlaceholderClassName = (
    `${isNumberInputGetValue ? 'input__placeholder' : 'input__placeholder input__placeholder_nonfocus'}`
  );

  const watchNumber = watch('number');

  useEffect(() => {
    if(watchNumber === '') {
      setIsNumberInputGetValue(false)
    } else {setIsNumberInputGetValue(true)}
  }, [watchNumber])

  return (
    <div >
      <div className='questionary__customer-container questionary__customer-container_name'>
        <TextInput
          title={'Имя'}
          placeholder={'Ведите свое имя'}
          nameInput={'firstname'}
          errorValue={REQUIRED_ERROR}
          type='text'
          register={register}
          errors={errors}
          />
        <TextInput
          title={'Фамилия'}
          placeholder={'Ведите свою фамилию'}
          nameInput={'lastname'}
          errorValue={REQUIRED_ERROR}
          type='text'
          register={register}
          errors={errors}
          />
        <TextInput
          title={'Отчество'}
          placeholder={'Ведите свое отчество'}
          nameInput={'middlename'}
          errorValue={REQUIRED_ERROR}
          type='text'
          register={register}
          errors={errors}
          />
      </div>
      <div className='questionary__customer-container questionary__customer-container_company'>
        <RadioFilter
          options={LEGAL_STATUS_OPTIONS}
          title={'Юридический статус'}
          register={register} 
          nameInput={'legalStatus'}
          errors={errors}
          errorValue={REQUIRED_ERROR}
        />
        <TextInput
          title={'Название организации'}
          placeholder={'Ведите название организации'}
          nameInput={'company'}
          errorValue={REQUIRED_ERROR}
          type='text'
          register={register}
          errors={errors}
        />
      </div>
      <div className='questionary__container questionary__container_contacts'>
        <TextInput
          title={'Адрес'}
          placeholder={'Введите адрес'}
          nameInput={'address'}
          errorValue={REQUIRED_ERROR}
          type='text'
          register={register}
          errors={errors}
          />
        <label className='input__title'>
          Номер телефона
          <span className={numberPlaceholderClassName}>+7</span>
          <input
            className='input input_number'
            type='number'
            {...register('number', {
              required: REQUIRED_ERROR,
              pattern: {
                value: /^\d{10}$/,
                message: INAVID_NUMBER_ERROR
              }
            })}
          />
          {errors.number && (
            <div className='input__error'>
              {errors.number.message}
            </div>
          )}
        </label>
      </div>
    </div>
  )
}

export default GeneralInfoCustomer;