import './Photos.css';
import { useForm } from "react-hook-form";
import { useState } from 'react'
import Popup from '../Popup/Popup';
import PhotoInputs from '../../../../components/PhotoInputs/PhotoInputs';

function Photos( { handleButtonBackClick }) {
  const [isPopupOpen, setIsPopupOpen] = useState(false);
  const [isAgree, setIsAgree] = useState(false)

  const closePopup = () => {
    setIsPopupOpen(false)
  }

  const handleButtonAgree = () => {
    setIsAgree(true)
  }

  const { 
    register, handleSubmit, formState: { errors }, reset
  } = useForm({
    mode: "onChange"
  });

  const onSubmit = (data) => {
    setIsPopupOpen(true)
  }

  return (
  <>
    <form className='questionary' onSubmit={handleSubmit(onSubmit)}>
      <h2 className='questionary__title'>Фото и видео</h2>
      <p className='questionary__subtitle'>Шаг 4 из 4</p>
      <div className='questionary__steps'>
        <span className='questionary__step questionary__step_passed'/>
        <span className='questionary__step questionary__step_passed'/>
        <span className='questionary__step questionary__step_passed'/>
        <span className='questionary__step questionary__step_passed'/>
      </div>
      <PhotoInputs register={register} errors={errors} isAgree={isAgree} reset={reset}/>
      <div className='question-list__buttons'>
        <button className='question-list__button question-list__button_back' onClick={handleButtonBackClick} type='button'>Назад</button>
        <button className='question-list__button question-list__button_next' type='submit'>Далее</button>
      </div>
    </form>
    <Popup onClose={closePopup} isOpen={isPopupOpen} onAgree={handleButtonAgree}/>
  </>
  )
}

export default Photos;