import './Popup.css';

function Popup ({ isOpen, onClose}) {
  return (
    <div
      onClick={e => (e.currentTarget === e.target) && onClose()}
      className={`popup-casting-confirm ${isOpen && "popup-casting-confirm_opened"}`}
    >
      <div className="popup-casting-confirm__container">
        <button className='popup-casting-confirm__button_close' onClick={onClose}/>
        <span className='popup-casting-confirm__icon'/>
        <h2 className='popup-casting-confirm__title'>Вы подтверждаете свой отклик на данную задачу?</h2>
        <p className='popup-casting-confirm__text'>
          Стоимость отклика 900 кастов
        </p>
        <div className='popup-casting-confirm__buttons'>
          <button className='popup-casting-confirm__button popup-casting-confirm__button_confirm' onClick={onClose}>Отказать</button>
          <button className='popup-casting-confirm__button popup-casting-confirm__button_refuse' onClick={onClose}>Подтвердить</button>
        </div>
      </div>
    </div>
  )
}

export default Popup;