import './AddMoney.css';
import Popup from '../Popup/Popup';
import { useState } from 'react';
import { useForm } from "react-hook-form";
import { ADD_MONEY_OPTIONS } from '../../../constants/filterOptions'
import { REQUIRED_ERROR } from '../../../constants/errors';
import TextInput from '../../../components/TextInput/TextInput';
import RadioFilter from '../../../components/RadioFilter/RadioFilter';

function AddMoney() {
  const [isPopupOpen, setIsPopupOpen] = useState(false);

  const { 
    register, handleSubmit, formState: { errors },
  } = useForm({
    mode: "onChange"
  });

  const closePopup = () => {
    setIsPopupOpen(false)
  }

  const handleButtonOpenPopup = (e) => {
    setIsPopupOpen(true)
  }

  const onSubmit = (data) => {
    console.log(data)
    handleButtonOpenPopup()
  }

  return (
    <>
      <form className='tabs__form add-money' onSubmit={handleSubmit(onSubmit)}>
        <p className='add-money__ratio'><span className='add-money__ratio-icon'/>Курс: 1 каст = 1 рубль</p>
        <div className='add-money__container'>
          <TextInput
            title={'Сумма пополнения'}
            placeholder={'Введите сумму'}
            nameInput={'sumValue'}
            errorValue={REQUIRED_ERROR}
            type='number'
            register={register}
            errors={errors}
            minValue='1'
            message='Введите положительное значение'
          />
          <RadioFilter
            options={ADD_MONEY_OPTIONS}
            title='Способ оплаты'
            register={register}
            nameInput={'typeAddMoney'}
            errors={errors}
            errorValue={REQUIRED_ERROR}
          />
        </div>
        <div className='add-money__card-data'>
          <TextInput
            placeholder={'Номер карты'}
            nameInput={'cardNumber'}
            errorValue={REQUIRED_ERROR}
            type='number'
            register={register}
            errors={errors}
            minValue='1'
            message='Введите положительное значение'
          />
          <div className='add-money__card-data-container'>
            <TextInput
              placeholder={'Владелец'}
              nameInput={'cardOwner'}
              errorValue={REQUIRED_ERROR}
              type='text'
              register={register}
              errors={errors}
            />
            <TextInput
              placeholder={'00/00'}
              nameInput={'cardDate'}
              errorValue={'Заполните'}
              type='number'
              register={register}
              errors={errors}
            />
            <TextInput
              placeholder={'CVC'}
              nameInput={'cvc'}
              errorValue={'Заполните'}
              type='text'
              register={register}
              errors={errors}
            />
          </div>
        </div>
        <button type='submit' className='add-money__button'>Пополнить</button>
      </form>
      <Popup onClose={closePopup} isOpen={isPopupOpen}/>
    </>
  )
}

export default AddMoney;