import './ProfileType.css';
import { useContext } from 'react';
import { REQUIRED_ERROR } from '../../../constants/errors';
import { CurrentUserContext } from '../../../contexts/CurrentUserContext';
import { BENEFITS_VERIFIED_PROFILE_ACTOR, BENEFITS_VERIFIED_PROFILE_CUSTOMER } from '../../../constants/const';

function ProfileType({ register, errors }) {
  const userType = useContext(CurrentUserContext);

  return (
    <>
      <p className='edit-profile__choose-type'>Выберите тип профиля</p>
      <div className='edit-profile__radio-input'>
        <label className='edit-profile__label'>
          <input
          {...register('profileTyp', {
            required: REQUIRED_ERROR,
          })}
          type='radio'
          value='unprivileged'
          className='edit-profile__input edit-profile__input_invisible'
          />
          <div className='edit-profile__input_visible'>
            <h3 className='edit-profile__input-title'>Обычный</h3>
            <ul  className='edit-profile__input-advantages'>
              <li className='edit-profile__input-advantage'>
                <span className='edit-profile__input-icon'/>
                <p className='edit-profile__input-text'>Бесплатный находится в общем списке по дате регистрации, Заказчики видят информацию анкеты и только фотки в свободной тематики 5 штук.</p>
              </li>
            </ul>
            <div className='edit-profile__input-price'>
              <p className='edit-profile__input-price-value'>Бесплатно</p>
            </div>
          </div>
        </label>
        <label className='edit-profile__label'>
          <input
          {...register('profileTyp', {
            required: REQUIRED_ERROR,
          })}
          type='radio'
          value='privileged'
          className='edit-profile__input edit-profile__input_invisible'
          />
          <div className='edit-profile__input_visible'>
            <h3 className='edit-profile__input-title'>Проверенный<span className='edit-profile__input-tick'/></h3>
            <ul  className='edit-profile__input-advantages'>
              <li className='edit-profile__input-advantage'>
                <span className='edit-profile__input-icon'/>
                <p className='edit-profile__input-text'>В профиле появляется значок «Верифицирован».</p>
              </li>
              {userType === 'actor' ?
              BENEFITS_VERIFIED_PROFILE_ACTOR.slice(0, BENEFITS_VERIFIED_PROFILE_ACTOR.length).map((benefit, index) =>
                (<li className='verification__benefit' key={index}>
                  <span className='verification__benefit-tick'/>
                  <p className='verification__benefit-text'>{benefit}</p>
                </li>)
              )
              :
              BENEFITS_VERIFIED_PROFILE_CUSTOMER.slice(0, BENEFITS_VERIFIED_PROFILE_CUSTOMER.length).map((benefit, index) =>
                (<li className='verification__benefit' key={index}>
                  <span className='verification__benefit-tick'/>
                  <p className='verification__benefit-text'>{benefit}</p>
                </li>)
              )
              }
            </ul>
            <div className='edit-profile__input-price'>
              <p className='edit-profile__input-price-value'>От 100 каст</p>
              <p className='edit-profile__input-rate'>1 каст = 1 рубль</p>
            </div>
          </div>
        </label>
          {errors.profileTyp && (
          <div className='input__error'>
            {errors.profileTyp.message}
          </div>
        )}
      </div>
    </>
  )
}

export default ProfileType;