import './PhotoInputs.css';
import { useState, useRef, useEffect } from 'react';
import { PHOTOS_ERROR, VIDEO_ERROR } from '../../constants/errors'

function PhotoInputs({ photoFilterClassName, register, errors, isAgree, reset }) {
  const questionsClassName = (
    `${photoFilterClassName ? photoFilterClassName : 'questionary__photo-block'}`
  )

  const [isFacePhotoLoad, setIsFacePhotoLoad] = useState(false)
  const [isProfilePhotoLoad, setIsProfilePhotoLoad] = useState(false)
  const [isBehindPhotoLoad, setIsBehindPhotoLoad] = useState(false)
  const [isFacePhotoUndressedLoad, setIsFacePhotoUndressedLoad] = useState(false)
  const [isProfilePhotoUndressedLoad, setIsProfilePhotoUndressedLoad] = useState(false)
  const [isBehindPhotoUndressedLoad, setIsBehindPhotoUndressedLoad] = useState(false)
  const [isPhotosArrayLoad, setIsPhotosArrayLoad] = useState(false)
  const [isVideoLoad, setIsVideoLoad] = useState(false)

  const [photoLength, setPhotoLength] = useState()

  const photoFaceRef = useRef(0);
  const photoProfileRef = useRef(0);
  const photoBehindRef = useRef(0);
  const photoFaceUndressedRef = useRef(0);
  const photoProfileUndressedRef = useRef(0);
  const photoBehindUndressedRef = useRef(0);

  const faceInput = register('face', {required: PHOTOS_ERROR});
  const profileInput = register('profile', {required: PHOTOS_ERROR});
  const behindInput = register('behind', {required: PHOTOS_ERROR});
  const faceUndressedInput = register('faceUndressed', {required: PHOTOS_ERROR});
  const profileUndressedInput = register('profileUndressed', {required: PHOTOS_ERROR});
  const behindUndressedInput = register('behindUndressed', {required: PHOTOS_ERROR});
  const photosArrayInput = register('photosArray', {required: PHOTOS_ERROR});
  const videoInput = register('video', {required: VIDEO_ERROR});
  
  useEffect(() => {
  if(isAgree === true) {
    reset()
    handleFacePhotoDelete()
    handleProfilePhotoDelete()
    handleBehindPhotoDelete()
    handleFacePhotoUndressedDelete()
    handleProfilePhotoUndressedDelete()
    handleBehindPhotoUndressedDelete()
    setIsPhotosArrayLoad(false)
    setIsVideoLoad(false)
    localStorage.removeItem('mainUserInfo')
    localStorage.removeItem('bodynUserInfo')
    localStorage.removeItem('interestsUserInfo')
  }}, [isAgree])

  const handleFileReader = (e, ref) => {
    ref.current.style.backgroundImage = "url(" + URL.createObjectURL(e.target.files[0]) + ")";
  }

  const handleFileDelete = (ref) => {
    ref.current.style.backgroundImage = 'none'
  }

  const handleFacePhotoLoad = (e) => {
    handleFileReader(e, photoFaceRef)
    setIsFacePhotoLoad(true)
  }

  const handleFacePhotoDelete = (e) => {
    handleFileDelete(photoFaceRef)
    setIsFacePhotoLoad(false)
  }

  const handleProfilePhotoLoad = (e) => {
    handleFileReader(e, photoProfileRef)
    setIsProfilePhotoLoad(true)
  }

  const handleProfilePhotoDelete = (e) => {
    handleFileDelete(photoProfileRef)
    setIsProfilePhotoLoad(false)
  }

  const handleBehindPhotoLoad = (e) => {
    handleFileReader(e, photoBehindRef)
    setIsBehindPhotoLoad(true)
  }

  const handleBehindPhotoDelete = (e) => {
    handleFileDelete(photoBehindRef)
    setIsBehindPhotoLoad(false)
  }

  const handleFacePhotoUndressedLoad = (e) => {
    handleFileReader(e, photoFaceUndressedRef)
    setIsFacePhotoUndressedLoad(true)
  }

  const handleFacePhotoUndressedDelete = (e) => {
    handleFileDelete(photoFaceUndressedRef)
    setIsFacePhotoUndressedLoad(false)
  }

  const handleProfilePhotoUndressedLoad = (e) => {
    handleFileReader(e, photoProfileUndressedRef)
    setIsProfilePhotoUndressedLoad(true)
  }

  const handleProfilePhotoUndressedDelete = (e) => {
    handleFileDelete(photoProfileUndressedRef)
    setIsProfilePhotoUndressedLoad(false)
  }

  const handleBehindPhotoUndressedLoad = (e) => {
    handleFileReader(e, photoBehindUndressedRef)
    setIsBehindPhotoUndressedLoad(true)
  }

  const handleBehindPhotoUndressedDelete = (e) => {
    handleFileDelete(photoBehindUndressedRef)
    setIsBehindPhotoUndressedLoad(false)
  }

  const handleVideoLoad = (e) => {
    setIsVideoLoad(true)
  }

  const handlePhotosArrayLoad = (e) => {
    setIsPhotosArrayLoad(true)
    setPhotoLength(e.target.files.length)
  }

  return (
    <div className={questionsClassName}>
      <h3 className='questionary__photo-title questionary__photo-title_dressed'>Загрузите фотографии в полный рост в одежде</h3>
      <label className='questionary__photo-label questionary__photo-label_first'>
        <input
        name={faceInput.name}
        ref={faceInput.ref}
        type='file'
        accept="image/*"
        className='questionary__photo-item_invisible'
        onChange={(e) => {
          faceInput.onChange(e)
          handleFacePhotoLoad(e);
        }}
        />
        {errors.face && (
          <div className='input__error'>
            {errors.face.message}
          </div>
        )}
        <div className='questionary__photo-item questionary__photo-item_face' ref={photoFaceRef}>
          {isFacePhotoLoad ?
          <button className='questionary__photo-button_delete' onClick={handleFacePhotoDelete}/>
          :
          <>
            <span className='questionary__photo-icon questionary__photo-icon_face'/>
            <p className='questionary__photo-description questionary__photo-description_face'>Лицом</p>
          </>
          }
        </div>
      </label>
      <label className='questionary__photo-label'>
        <input
        name={profileInput.name}
        ref={profileInput.ref}
        type='file'
        accept="image/*"
        className='questionary__photo-item_invisible'
        onChange={(e) => {
          profileInput.onChange(e)
          handleProfilePhotoLoad(e);
        }}
        />
        {errors.profile && (
          <div className='input__error'>
            {errors.profile.message}
          </div>
        )}
        <div className='questionary__photo-item questionary__photo-item_profile' ref={photoProfileRef}>
          {isProfilePhotoLoad ?
          <button className='questionary__photo-button_delete' onClick={handleProfilePhotoDelete}/>
          :
            <>
            <span className='questionary__photo-icon questionary__photo-icon_profile'/>
            <p className='questionary__photo-description questionary__photo-description_profile'>Боком</p>
            </>
            }
        </div>
      </label>
      <label className='questionary__photo-label'>
        <input
        name={behindInput.name}
        ref={behindInput.ref}
        type='file'
        accept="image/*"
        className='questionary__photo-item_invisible'
        onChange={(e) => {
          profileInput.onChange(e)
          handleBehindPhotoLoad(e);
        }}
        />
        {errors.behind && (
        <div className='input__error'>
          {errors.behind.message}
        </div>
        )}
        <div className='questionary__photo-item questionary__photo-item_behind' ref={photoBehindRef}>
          {isBehindPhotoLoad ?
          <button className='questionary__photo-button_delete' onClick={handleBehindPhotoDelete}/>
          :
          <>
            <span className='questionary__photo-icon questionary__photo-icon_behind'/>
            <p className='questionary__photo-description questionary__photo-description_behind'>Сзади</p>
          </>
          }
        </div>
      </label>
      <h3 className='questionary__photo-subtitle'>Загрузите фотографии свободной тематики</h3>
      <label className='questionary__photo-label questionary__photo-item_photos'>
        <input
        name={photosArrayInput.name}
        ref={photosArrayInput.ref}
        type='file'
        accept="image/*"
        multiple
        className='questionary__photo-item_invisible'
        onChange={(e) => {
          photosArrayInput.onChange(e)
          handlePhotosArrayLoad(e)
        }}
        />
        {errors.photosArray && (
        <div className='input__error'>
          {errors.photosArray.message}
        </div>
        )}
        <div className='questionary__photo-item questionary__photo-item_many-elements'>
          {isPhotosArrayLoad ? 
          <p className='questionary__photo-load'>{`Загружено ${photoLength} фото`}</p>
          :
          <>
            <span className='questionary__photo-icon'/>
            <p className='questionary__photo-description questionary__photo-description_photos'>До 5 штук</p>
          </>
            }
        </div>
      </label>
      <h3 className='questionary__photo-title questionary__photo-title_undressed'>Загрузите фотографии в полный рост в нижнем белье</h3>
      <label className='questionary__photo-label'>
        <input
        name={faceUndressedInput.name}
        ref={faceUndressedInput.ref}
        type='file'
        accept="image/*"
        className='questionary__photo-item_invisible'
        onChange={(e) => {
          faceUndressedInput.onChange(e)
          handleFacePhotoUndressedLoad(e);
        }}
        />
        {errors.faceUndressed && (
        <div className='input__error'>
          {errors.faceUndressed.message}
        </div>
        )}
        <div className='questionary__photo-item questionary__photo-item_face-undressed' ref={photoFaceUndressedRef}>
          {isFacePhotoUndressedLoad ?
            <button className='questionary__photo-button_delete' onClick={handleFacePhotoUndressedDelete}/>
            :
            <>
              <span className='questionary__photo-icon'/>
              <p className='questionary__photo-description questionary__photo-description_face'>Лицом</p>
            </>
          }
          </div>
      </label>
      <label className='questionary__photo-label'>
        <input
        name={profileUndressedInput.name}
        ref={profileUndressedInput.ref}
        type='file'
        accept="image/*"
        className='questionary__photo-item_invisible'
        onChange={(e) => {
          profileUndressedInput.onChange(e)
          handleProfilePhotoUndressedLoad(e);
        }}
        />
        {errors.profileUndressed && (
        <div className='input__error'>
          {errors.profileUndressed.message}
        </div>
        )}
        <div className='questionary__photo-item questionary__photo-item_profile-undressed' ref={photoProfileUndressedRef}>
          {isProfilePhotoUndressedLoad ?
            <button className='questionary__photo-button_delete' onClick={handleProfilePhotoUndressedDelete}/>
            :
            <>
              <span className='questionary__photo-icon'/>
              <p className='questionary__photo-description questionary__photo-description_profile'>Боком</p>
            </>
          }
        </div>
      </label>
      <label className='questionary__photo-label'>
        <input
        type='file'
        accept="image/*"
        className='questionary__photo-item_invisible'
        name={behindUndressedInput.name}
        ref={behindUndressedInput.ref}
        onChange={(e) => {
          behindUndressedInput.onChange(e)
          handleBehindPhotoUndressedLoad(e);
        }}
        />
        {errors.behindUndressed && (
        <div className='input__error'>
          {errors.behindUndressed.message}
        </div>
        )}
        <div className='questionary__photo-item questionary__photo-item_behind-undressed' ref={photoBehindUndressedRef}>
          {isBehindPhotoUndressedLoad ?
            <button className='questionary__photo-button_delete' onClick={handleBehindPhotoUndressedDelete}/>
            :
            <>
              <span className='questionary__photo-icon'/>
              <p className='questionary__photo-description questionary__photo-description_behind'>Сзади</p>
            </>
          }
        </div>
      </label>
      <h3 className='questionary__video-subtitle'>Загрузите промо ролик свободной тематики</h3>
      <label className='questionary__photo-label questionary__photo-item_videos'>
        <input
        name={videoInput.name}
        ref={videoInput.ref}
        type='file'
        accept="video/*"
        className='questionary__photo-item_invisible'
        onChange={(e) => {
          videoInput.onChange(e)
          handleVideoLoad(e);
        }}
        />
        {errors.video && (
        <div className='input__error'>
          {errors.video.message}
        </div>
        )}
        <div className='questionary__photo-item questionary__photo-item_many-elements'>
          {isVideoLoad ?
          <p className='questionary__photo-load'>Промо ролик загружен</p>
          :
          <>
            <span className='questionary__photo-icon'/>
            <p className='questionary__photo-description questionary__photo-description_photos'>Длительность от 30 секунд до 1 минуты</p>
          </>
          }
        </div>
      </label>
    </div>
  )
}

export default PhotoInputs;