import './Form.css';
import { useState } from 'react'
import MainInfo from './MainInfo/MainInfo';
import BodyInfo from './BodyInfo/BodyInfo';
import InterestsInfo from './InterestsInfo/InterestsInfo';
import Photos from './Photos/Photos';

function Form() {
  const [page, setPage] = useState(1);

  function handleButtonNextClick() {
    setPage(page+1)
  };

  function handleButtonBackClick() {
    setPage(page-1)
  };

  let step = null;

  if (page === 1) {
    step = <MainInfo handleButtonNextClick={handleButtonNextClick}/>
  } else if (page === 2) {
    step = <BodyInfo handleButtonBackClick={handleButtonBackClick} handleButtonNextClick={handleButtonNextClick}/>
  } else if (page === 3) {
    step = <InterestsInfo handleButtonBackClick={handleButtonBackClick} handleButtonNextClick={handleButtonNextClick}/>
  } else if (page === 4) {
    step = <Photos handleButtonBackClick={handleButtonBackClick}/>
  }

  return (
    <>
      <div className='question-list__form'>
        {step}
      </div>
    </>
  )
}

export default Form;