import './ReviewsCasting.css';
import Review from '../../../components/Review/Review';

function ReviewsCasting ( {reviews} ) {
  return (
    <div className='reviews-casting'>
      <p className='reviews-casting__subtitle'>Заказ</p>
      <h2 className='reviews-title'>Нужен молой человек с рыжими волосами для рекламы шампуня</h2>
      <p className='reviews-casting__subtitle'>Отзывы</p>
      <ul className='revies-casting__review'>
        {reviews.slice(0, reviews.length).map((review) =>
          (<Review review={review}/>)
        )}
      </ul>
    </div>

  )
}

export default ReviewsCasting;