import './Form.css';
import { NavLink } from 'react-router-dom';
import { USER_TYPE_OPTIONS } from '../../../constants/filterOptions';
import { ROUTES } from '../../../constants/routes';
import { useState } from 'react';
import { useForm } from "react-hook-form";

function Form({handleRegister}) {
  const [typePasswordInput, setPasswordTypeInput] = useState ('password');
  const [typeRepeatPasswordInput, setRepeatPasswordTypeInput] = useState ('password');
  const [classNamePasswordInput, setClassNamePasswordInput] = useState('auth__input-icon_lock');
  const [classNameRepeatPasswordInput, setClassNameRepeatPasswordInput] = useState('auth__input-icon_lock');

  const { 
    register, handleSubmit, formState: { errors },
  } = useForm({
    mode: "onChange"
  });

  const onSubmit = (data) => {
    handleRegister()
  }

  const handleUserTypeChange = (e) => {
    
  }

  const togglePassInput = (e) => {
    if(typePasswordInput === 'password') {
      setPasswordTypeInput('text')
      setClassNamePasswordInput('auth__input-icon_unlock')
    } else {
      setPasswordTypeInput('password')
      setClassNamePasswordInput('auth__input-icon_lock')
    }
  }

  const toggleRepeatPassInput = (e) => {
    if(typeRepeatPasswordInput === 'password') {
      setRepeatPasswordTypeInput('text')
      setClassNameRepeatPasswordInput('auth__input-icon_unlock')
    } else {
      setRepeatPasswordTypeInput('password')
      setClassNameRepeatPasswordInput('auth__input-icon_lock')
    }
  }

  return (
    <section className='auth__container auth__container_register'>
      <form className='auth__form auth__form_register' onSubmit={handleSubmit(onSubmit)}>
        <h1 className='auth__title auth__title_register'>Регистрация</h1>
        <div className='filter-radio'>
          <div className='filter-radio__container filter-radio__container_component_register'>
            <label className='filter-radio__label'>
              <input
                {...register('userType')}
                type='radio'
                name='radio'
                value={USER_TYPE_OPTIONS[0].value}
                className='filter-radio__input filter-radio__input_invisible'
                onChange={handleUserTypeChange}
                defaultChecked
              />
              <span className='filter-radio__input_visible'/>
              <p className='filter-radio__label-subtitle filter-radio__label-subtitle_component_register'>{USER_TYPE_OPTIONS[0].label}</p>
            </label>
            <label className='filter-radio__label'>
              <input
                {...register('userType')}
                type='radio'
                name='radio'
                value={USER_TYPE_OPTIONS[1].value}
                className='filter-radio__input filter-radio__input_invisible'
                onChange={handleUserTypeChange}
              />
              <span className='filter-radio__input_visible'/>
              <p className='filter-radio__label-subtitle filter-radio__label-subtitle_component_register'>{USER_TYPE_OPTIONS[1].label}</p>
            </label>
          </div>
        </div>
        <label className='auth__label'>
          <input
            {...register('email', {
              required: 'Поле обязательно к заполнению',
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                message: "Поле должно содержать email"
              }
            })}
            className='auth__input auth__input_login'
            placeholder='Email'
            type='email'
          />
          {errors?.email && (
            <div className='auth__error'>
              {errors.email.message}
            </div>
          )}
        </label>
        <label className='auth__label'>
          <input
            {...register('password', {
              required: 'Поле обязательно к заполнению',
            })}
            className='auth__input auth__input_register'
            placeholder='Придумайте пароль'
            type={typePasswordInput}
          />
          <button className={`auth__input-icon ${classNamePasswordInput}`} onClick={togglePassInput} type='button'/>
          {errors?.password && (
            <div className='auth__error'>
              {errors.password.message}
            </div>
          )}
        </label>
        <label className='auth__label'>
          <input
            {...register('repeatPassword', {
              required: 'Поле обязательно к заполнению',
            })}
            className='auth__input auth__input_register'
            placeholder='Повторите пароль'
            type={typeRepeatPasswordInput}
          />
          <button className={`auth__input-icon ${classNameRepeatPasswordInput}`} onClick={toggleRepeatPassInput} type='button'/>
          {errors?.repeatPassword && (
            <div className='auth__error'>
              {errors.repeatPassword.message}
            </div>
          )}
        </label>
        <button className='auth__button auth__button_register' type='submit'>Зарегистрироваться</button>
      </form>
      <NavLink to={ROUTES.LOGIN} className='auth__link auth__link_register'>Войти</NavLink>
    </section>
  )
}

export default Form;