import './TextInput.css';

function TextInput({ title, placeholder, inputClassName, register, nameInput, type, errors, errorValue, minValue, message, minLength, minLengthError, pattern }) {
  return (
    <label className={'input__title ' + inputClassName}>
      {title}
      <input className='input'
        placeholder={placeholder}
        type={type}
        {...register(nameInput,
          errorValue ?
          {
            required: errorValue,
           min: {
              value: minValue,
              message: message,
           },
           minLength: {
            value: minLength,
            message: minLengthError,
          },
          pattern: {
            value: pattern,
            message: message
          }
        } : '')}
      />
      {errors &&
        errors[nameInput] && (
          <div className='input__error'>
            {errors[nameInput].message}
          </div>
        )
      }
    </label>
  );
}

export default TextInput;