import './Notification.css';
import { Link } from 'react-router-dom';

function Notification({ date, text, newNotice, link }) {

  const noticeClassName = (
    `notice ${newNotice === 'true' && 'notice_new-notice'}`
  )

  return (
    <li className={noticeClassName}>
      <p className='notice__date'>{date}</p>
      <h3 className='notice__text'>
        {text} <Link to={link} className='notice__link'>проекта</Link>
      </h3>
      <span className='notice__icon notice__icon_visible'/>
    </li>
  )
}

export default Notification;