import './ThemeList.css';
import { INTERESTS_ERROR } from '../../constants/errors';

function ThemeList({ options, errors, register }) {
  return (
      <ul className='theme-list'>
          {options.slice(0, options.length).map((option, index) => (
            <li className='theme-list__item' key={option.id}>
              <label className='theme-list__label'>
                <input
                  {...register('themes', {
                    required: INTERESTS_ERROR,
                  })}
                  className='theme-list__input_invisible'
                  type='checkbox'
                  value={option.value}
                  />
                <span className='theme-list__input_visible'>{option.label}</span>
              </label>
              </li>
          ))}
          {errors.themes && (
          <div className='input__error'>
            {errors.themes.message}
          </div>
          )}
    </ul>

  )
}

export default ThemeList;