import './HistoryItem.css';

function HistoryItem({ date, type, sum, state }) {
  const stateClassName = (
    `${state === 'Выполненно' ? 'history-item__value_green' : 'history-item__value_grey'}`
  ) 
  return (
    <li className='history-item'>
      <p className='history-item__value history-item__value_date'>{date}</p>
      <p className='history-item__value history-item__value_type'>{type}</p>
      <p className='history-item__value history-item__value_sum'>{sum}</p>
      <p className={stateClassName}>{state}</p>
    </li>
  )
}

export default HistoryItem;