import './Transfer.css';
import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { REQUIRED_ERROR } from '../../../constants/errors';
import TextInput from '../../../components/TextInput/TextInput';
import Popup from '../Popup/Popup';

function Transfer() {
  const [isPopupOpen, setIsPopupOpen] = useState(false);

  const { 
    register, handleSubmit, formState: { errors },
  } = useForm({
    mode: "onChange"
  });

  const closePopup = () => {
    setIsPopupOpen(false)
  }

  const handleButtonOpenPopup = (e) => {
    setIsPopupOpen(true)
  }

  const onSubmit = (data) => {
    console.log(data)
    handleButtonOpenPopup()
  }

  return (
    <>
      <form className='tabs__form' onSubmit={handleSubmit(onSubmit)}>
        <div className='transfer__container'>
          <label className='input__title'>
            Email актера
            <input
              {...register('email', {
                required: 'Поле обязательно к заполнению',
                pattern: {
                  value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                  message: "Поле должно содержать email"
                }
              })}
              className='input'
              placeholder='Введите свой email'
              type='email'
            />
            {errors?.email && (
              <div className='input__error'>
                {errors.email.message}
              </div>
            )}
            {!errors.email && <p className='transfer__text'>Он должен быть авторизован на сервисе</p>}
          </label>
          <TextInput
            title={'Сумма перевода в кастах'}
            placeholder={'Введите сумму'}
            nameInput={'sum'}
            errorValue={REQUIRED_ERROR}
            type='number'
            register={register}
            errors={errors}
            minValue='1'
            message='Введите положительное значение'
          />
        </div>
        <button type='submit' className='transfer__button'>Отправить</button>
      </form>
      <Popup onClose={closePopup} isOpen={isPopupOpen}/>
    </>
  )
}

export default Transfer;