import './TextareaInput.css';

function TextareaInput({ title, placeholder, register, nameInput, errors, errorValue }) {
  return (
    <label className='textaera'>
      {title}
      <textarea className='textaera__input'
        placeholder={placeholder}
        type='textarea'
        {...register(nameInput, {
          required: errorValue,
        })}
      />
      {errors[nameInput] && (
        <div className='input__error'>
          {errors[nameInput].message}
        </div>
      )}
    </label>
  )
}

export default TextareaInput;