import './EditProfile.css';
import { useContext } from 'react';
import { useForm } from "react-hook-form";
import { CurrentUserContext } from '../../contexts/CurrentUserContext';
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import EditActorProfile from './EditActorProfile/EditActorProfile';
import EditCustomerProfile from './EditCustomerProfile/EditCustomerProfile';

function EditProfile({ loggedIn }) {
  const userType = useContext(CurrentUserContext);

  const { 
    register, handleSubmit, formState: { errors }, control, watch
  } = useForm({
    mode: "onChange",
  });

  const onSubmit = (data) => {
    console.log(data)
  }

  return (
    <>
      <Header loggedIn={loggedIn}/>
      <main className='content content_edit-profile edit-profile'>
        <h1 className='edit-profile__title'>Редактирование профиля</h1>
        <form className='edit-profile__form' onSubmit={handleSubmit(onSubmit)}>
          {userType === 'actor' ?
          <EditActorProfile register={register} control={control} watch={watch} errors={errors}/>
          :
          <EditCustomerProfile register={register} control={control} watch={watch} errors={errors}/>
          }
          <button className='edit-profile__save-button' type='submit'>Сохранить</button>
        </form>
      </main>
      <Footer />
    </>
  )
}

export default EditProfile;