import './ProfileVerification.css';
import { useContext, useRef, useState } from 'react';
import { useForm } from "react-hook-form";
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import { CurrentUserContext } from '../../contexts/CurrentUserContext';
import GoBackButton from '../../components/GoBackButton/GoBackButton';
import { BENEFITS_VERIFIED_PROFILE_ACTOR, BENEFITS_VERIFIED_PROFILE_CUSTOMER } from '../../constants/const';
import { PHOTOS_ERROR } from '../../constants/errors';

function ProfileVerification ({ loggedIn }) {
  const userType = useContext(CurrentUserContext);
  const { 
    register, handleSubmit, formState: { errors }
  } = useForm({
    mode: "onChange"
  });

  const photoPassportInput = register('photoPassport', {required: PHOTOS_ERROR});
  const photoPassportWithPersonInput = register('photoPassportWithPerson', {required: PHOTOS_ERROR});

  const photoPassportRef = useRef(0);
  const photoPassportWithPersonRef = useRef(0);

  const [isPassportPhotoLoad, setIsPassportPhotoLoad] = useState(false)
  const [isPassportWithPersonPhotoLoad, setIsPassportWithPersonPhotoLoad] = useState(false)

  const handleFileReader = (e, ref) => {
    ref.current.style.backgroundImage = "url(" + URL.createObjectURL(e.target.files[0]) + ")";
  }

  const handleFileDelete = (ref) => {
    ref.current.style.backgroundImage = 'none'
  }

  const handlePassportPhotoLoad = (e) => {
    handleFileReader(e, photoPassportRef)
    setIsPassportPhotoLoad(true)
  }

  const handlePassportPhotoDelete = (e) => {
    handleFileDelete(photoPassportRef)
    setIsPassportPhotoLoad(false)
  }

  const handlePassportWithPersonPhotoLoad = (e) => {
    handleFileReader(e, photoPassportWithPersonRef)
    setIsPassportWithPersonPhotoLoad(true)
  }

  const handlePassportWithPersonPhotoDelete = (e) => {
    handleFileDelete(photoPassportWithPersonRef)
    setIsPassportWithPersonPhotoLoad(false)
  }

  const onSubmit = (data) => {
    console.log(data)
  }

  return (
    <>
      <Header loggedIn={loggedIn}/>
      <main className='content content_verification verification'>
        <GoBackButton />
        <h1 className='verification__title'>Верификация профиля</h1>
        <section className='verification__benefits'>
          <h2 className='verification__benefits-title'>Преимущества проверенного профиля</h2>
          {userType==='actor' ? 
          <ul className='verification__benefits-list'>
            <li className='verification__benefit verification__benefit_with-tick'>
              <span className='verification__benefit-tick'/>
              <p className='verification__benefit-text'>
                В профиле появляется значок «Верифицирован»
                <span className='verification__benefit-icon'/>
              </p>
            </li>
            {BENEFITS_VERIFIED_PROFILE_ACTOR.slice(0, BENEFITS_VERIFIED_PROFILE_ACTOR.length).map((benefit, index) =>
              (<li className='verification__benefit' key={index}>
                <span className='verification__benefit-tick'/>
                <p className='verification__benefit-text'>{benefit}</p>
              </li>)
            )}
          </ul>
          :
          <ul className='verification__benefits-list'>
            <li className='verification__benefit verification__benefit_with-tick'>
              <span className='verification__benefit-tick'/>
              <p className='verification__benefit-text'>
                В профиле появляется значок «Верифицирован»
                <span className='verification__benefit-icon'/>
              </p>
            </li>
            {BENEFITS_VERIFIED_PROFILE_CUSTOMER.slice(0, BENEFITS_VERIFIED_PROFILE_CUSTOMER.length).map((benefit, index) =>
              (<li className='verification__benefit' key={index}>
                <span className='verification__benefit-tick'/>
                <p className='verification__benefit-text'>{benefit}</p>
              </li>)
            )}
          </ul>
          }
          <div className='verification__container'>
            <p className='verification__price'>{userType==='actor' ? '350 каст / мес' : '450 каст / мес'}</p>
            <p className='verification__rate'>1 каст = 1 рубль</p>
          </div>
        </section>
        <form className='verification__form' onSubmit={handleSubmit(onSubmit)}>
          <h3 className='verification__form-title verification__form-title_main'>Загрузите фото или скан главной страницы паспорта</h3>
          <label className='verification__form-label verification__form-label_passport-photo'>
            <input
            name={photoPassportInput.name}
            ref={photoPassportInput.ref}
            type='file'
            accept="image/*"
            className='verification__form-input_invisible'
            onChange={(e) => {
              photoPassportInput.onChange(e)
              handlePassportPhotoLoad(e)
            }}
            />
            {errors.photoPassport && (
            <div className='input__error'>
              {errors.photoPassport.message}
            </div>
            )}
            <div className='verification__form-input' ref={photoPassportRef}>
              {isPassportPhotoLoad ? 
              <button className='verification__form-button_delete' onClick={handlePassportPhotoDelete}/>
              :
              <>
                <span className='verification__form-icon'/>
                <p className='verification__form-subtitle'>Фото паспорта</p>
              </>
              }
            </div>
          </label>
          <h3 className='verification__form-title verification__form-title_passport-with-person'>Загрузите ваше фото с развернутым паспортом в руках</h3>
          <label className='verification__form-label verification__form-label_passport-with-person-photo'>
            <input
            name={photoPassportWithPersonInput.name}
            ref={photoPassportWithPersonInput.ref}
            type='file'
            accept="image/*"
            className='verification__form-input_invisible'
            onChange={(e) => {
              photoPassportWithPersonInput.onChange(e)
              handlePassportWithPersonPhotoLoad(e)
            }}
            />
            {errors.photoPassportWithPerson && (
            <div className='input__error'>
              {errors.photoPassportWithPerson.message}
            </div>
            )}
            <div className='verification__form-input' ref={photoPassportWithPersonRef}>
              {isPassportWithPersonPhotoLoad ? 
              <button className='verification__form-button_delete' onClick={handlePassportWithPersonPhotoDelete}/>
              :
              <>
                <span className='verification__form-icon'/>
                <p className='verification__form-subtitle'>Фото паспорта</p>
              </>
              }
            </div>
          </label>
          <button className='verification__form-button' type='submit'>{userType==='actor' ? 'Заплатить 350 каст' : 'Заплатить 450 каст'}</button>
        </form>
      </main>
      <Footer />
    </>

  )
}

export default ProfileVerification;