import { useState, useContext } from 'react';
import { useLastLocation } from 'react-router-last-location';
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import GoBackButton from '../../components/GoBackButton/GoBackButton';
import balanceIcon from '../../images/balance-icon.svg';
import Tabs from '../../components/Tabs/Tabs';
import WithdrawFunds from './WithdrawFunds/WithdrawFunds';
import AddMoney from './AddMoney/AddMoney';
import History from './History/History';
import Transfer from './Transfer/Transfer';
import { TABS_FINANCE_USER_ACTOR, TABS_FINANCE_USER_CUSTOMER } from '../../constants/const';
import { CurrentUserContext } from '../../contexts/CurrentUserContext';

import './Finance.css';

function Finance ({loggedIn}) {
  const userType = useContext(CurrentUserContext);
  const lastLocation = useLastLocation();

  const tab = (
    `${lastLocation !== null ? lastLocation.pathname === '/profile-promotion' || '/new-casting' ? 'add-money' : 'withdraw' : 'withdraw'}`
  )

  const [ activeTab, setActiveTab ] = useState(tab);

  function handleTab(e) {
    setActiveTab(e.target.getAttribute('data-name'));
  }

  const FinanceTab = () => {
    if(activeTab === 'withdraw') {
      return <WithdrawFunds />
    } else if (activeTab === 'add-money') {
      return <AddMoney />
    } else if (activeTab === 'transfer') {
      return <Transfer />
    } else {
      return <History />
    }
  }

  return (
    <>
      <Header loggedIn={loggedIn}/>
      <main className='content content_finance finance'>
        <div className='finance__container'>
          <GoBackButton />
          <div className='finance__balance'>
            <img className='finance__balance-icon' src={balanceIcon} alt='Монета'/>
            <h3 className='finance__balance-title'>Баланс</h3>
            <p className='finance__balance-subtitle'>12 000 каст</p>
          </div>
          <h1 className='finance__title'>Финансы</h1>
        </div>
        <section className="tabs">
          <Tabs
            handleButtonClick={handleTab}
            activeTab={activeTab}
            tabs={userType === 'actor' ? TABS_FINANCE_USER_ACTOR : TABS_FINANCE_USER_CUSTOMER}/>
          <FinanceTab />
        </section>
      </main>
      <Footer />
    </>
  )
}

export default Finance;