import './Promo.css';
import Actor from '../Actor/Actor';
import photo from '../../images/Photo/4.png';
import useScreenWidth from "../../utils/useScreenWidth";
import { useLocation } from 'react-router-dom';
import PromoItem from './PromoItem/PromoItem';
import PromoCasting from './PromoCasting/PromoCasting';

function Promo() {
  const size =  useScreenWidth();
  const location = useLocation();

  return (
    <section className='promo'>
      <ul className='promo__list'>
        {
          location.pathname === '/' ?
          <>
            <PromoItem />
            {
              size.width > 992 ?
              <Actor photo={photo} className='actor actor_postion_promo' buttonClassName='actor__play-video actor__play-video_postion_promo' videoClassName='actor__video actor__video_postion_promo'/>
              : ''
            }
          </>
          :
          <>
            <PromoCasting
              city={'Москва'}
              price={'150 000'}
              title={'Срочно нужны актеры с опытом в рекламе'}
              tags={['Кинематограф']}
            />
            <PromoCasting
              city={'Москва'}
              price={'5 000'}
              title={'Нужны с опытом в сериале. Царские времена. Девушки.'}
              tags={['Кинематограф', 'Массовка']}
            />
            <PromoItem />
          </>
        }
      </ul>
      <p className='promo__subtitle'>Реклама на сайте</p>
    </section>
  );
}

export default Promo;