import './SliderItem.css';

function SliderItem ({ photo, openPopup, setInitialPhoto }) {
  const handlePhotoClick = () => {
    openPopup()
    setInitialPhoto(photo)
  }

  return (
    <div key={photo.id}>
      <img src={photo.src} alt={photo.title} onClick={handlePhotoClick} className='slider-item__photo'></img>
    </div>
  )
}

export default SliderItem;