import './MyCastings.css';
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import GoBackButton from '../../components/GoBackButton/GoBackButton';
import MyCasting from './MyCasting/MyCasting';
import Tabs from '../../components/Tabs/Tabs';
import { useState } from 'react';
import { TABS_CASTINGS_COMPONENT, CASTINGS } from '../../constants/const';

function MyCastings({ loggedIn, onCastingClick }) {
  const [ activeTab, setActiveTab ] = useState('all');

  function handleTab(e) {
    setActiveTab(e.target.getAttribute('data-name'));
  }

  return (
    <>
      <Header loggedIn={loggedIn}/>
        <main className='content content_my-castings my-castings'>
          <GoBackButton />
          <h1 className='my-castings__title'>Мои кастинги</h1>
          <Tabs handleButtonClick={handleTab} activeTab={activeTab} tabs={TABS_CASTINGS_COMPONENT}/>
          <section className='my-castings__list'>
            {CASTINGS.slice(0, CASTINGS.length).map((casting) =>
              (<MyCasting casting={casting} key={casting.id} onCastingClick={onCastingClick}/>)
            )}
            <button className='my-castings__button-load-more'>Показать еще</button>
          </section>
        </main>
      <Footer />
    </>
  )
}

export default MyCastings;